<template>
  <div>
    <el-container>
      <el-aside width="380px" class="treeContainer">
        <p class="leftTitle">设备管理</p>
        <p class="leftSmallTitle">用户列表</p>
        <mainTree :data="data" v-on:getElectricBox="showElectricBox" v-on:getElectricBoxdata="showElectricBoxdata"></mainTree>
      </el-aside>
      <el-main class="dataContainer">
        <el-container>
          <el-header height="300px" style="padding: 0">
            <div class="buttonArr" style="margin-bottom:10px">
              <span class="monitorTitle">监控列表</span>
              <div class="operationButton" @click="isAddEquipmen()">
                <img src="../../../static/img/添加.png" alt="">
                <span class="operationstr">添加</span>
              </div>
              <div class="operationButton"  @click="isDelete()">
                <img src="../../../static/img/删除.png" alt="">
                <span class="operationstr">删除</span>
              </div>
              <div class="operationButton" @click="isupdata()">
                <img src="../../../static/img/修改.png" alt="">
                <span class="operationstr">修改</span>
              </div>
              <div class="operationButton" @click="showLoop()">
                <img src="../../../static/img/关联.png" alt="">
                <span class="operationstr">回路信息</span>
              </div>
            </div>          
            <el-table
              ref="singleTable"
              :data="electricBoxdata"
              highlight-current-row
              @current-change="handleCurrentChange"
              style="width: 100%; font-size:13px"
              >
              <el-table-column
                type="index">
              </el-table-column>
              <el-table-column
                property="name"
                label="设备名字">
              </el-table-column>
              <el-table-column
                property="code"
                label="设备编码">
              </el-table-column>
              <el-table-column
                property="temperatureValue"
                label="温度阈值">
              </el-table-column>
              <el-table-column
                property="isOnline"
                label="在线状态">
                <template slot-scope="scope">
                  <div class="name-wrapper">
                    {{ scope.row.isOnlin === 1 ? '在线' : '下线' }}
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="pageCenter">
              <el-pagination layout="prev, pager, next" :total="monitorTotalPage" :page-size='4' @current-change="monitorPageChange"></el-pagination>
            </div>
          </el-header>
          <el-container class="mainBottom">         
            <el-aside width="500px" class="bottomLeft">
              <div class="buttonArr" style="margin-bottom:10px">
                <span class="monitorTitle">电箱管理员</span>
                <div class="operationButtonpeole"  @click="isDeletePeole()">
                  <img src="../../../static/img/删除.png" alt="">
                  <span class="operationstr">删除</span>
                </div>
                <div class="operationButtonpeole" @click="isRelation()">
                  <img src="../../../static/img/修改.png" alt="">
                  <span class="operationstr">关联</span>
                </div>
              </div>
              <el-table
                ref="singleTable"
                :data="peopleTableData"
                highlight-current-row
                @current-change="handleCurrentChangePeople"
                style="width: 80%">
                <el-table-column
                  type="index"
                  width="50">
                </el-table-column>
                <el-table-column
                  property="userRealName"
                  label="姓名"
                  width="70">
                </el-table-column>
                <el-table-column
                  property="phone"
                  label="电话号码">
                </el-table-column>
                <el-table-column
                  property='date'
                  label="关联时间"
                  width="120">
                  <template slot-scope="scope">
                    <div class="name-wrapper">
                      {{relationDate(scope.row.date)}}
                    </div>
                  </template>
                </el-table-column>
              </el-table>
              <div class="block">
                <el-pagination layout="prev, pager, next" :total="peopletotalPage" @current-change="managerCurrentChange" :page-size="2">
                </el-pagination>
              </div>
            </el-aside>
            <el-main class="bottomRight">
              <span style="font-size:18px;margin-bottom:10px;">配电箱位置</span>
              <div class="amap-page-container" style="margin-top:10px">
                <el-amap ref="map" vid="amapDemo" :center="center" :zoom="zoom" :events="events" class="amap-demo">
                  <el-amap-marker :events="marker.events" :position="marker.position"></el-amap-marker>
                  <el-amap-info-window :position="theWindow.position" :visible="theWindow.visible" v-if="showWindow">
                    <div class="markWindow">
                      <span class="markerTitle">位置信息</span><br/><br/>
                      <span class="markerTitle">电箱名称：</span><span>{{mapMessage.name}}</span><br/>
                      <span class="markerTitle">机构：</span><span>{{mapMessage.mechanism}}</span><br/>
                      <span class="markerTitle">地址：</span><span>{{mapMessage.address}}</span>
                    </div>
                  </el-amap-info-window>
                </el-amap>
              </div>
            </el-main>
          </el-container>
        </el-container>
      </el-main>

          <!-- 增加和修改设备表单弹窗 -->
      <el-dialog :title="equipmentTitle" :visible.sync="addAndUpdataVisible">
        <el-form :model="form" :rules="boxRules" ref="boxform">
          <el-form-item label="设备名字" :label-width="formLabelWidth" prop="name">
            <el-input v-model="form.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="设备编码" :label-width="formLabelWidth" prop="code">
            <el-input v-model="form.code" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="温度阈值" :label-width="formLabelWidth" prop="temperatureValue">
            <el-input v-model="form.temperatureValue" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addAndUpdataVisible = false">取 消</el-button>
          <el-button type="primary" @click="addOrUpdata('boxform')">{{equipmentBtType}}</el-button>
        </div>
      </el-dialog>

          <!-- 删除设备弹窗 -->
      <el-dialog title="确定要删除该设备吗？" :visible.sync="deleteVisible">
        <el-table :data="theDeleteUser">
          <el-table-column property="name" label="设备名字"></el-table-column>
          <el-table-column property="code" label="设备编码"></el-table-column>
          <el-table-column property="isOnline" label="在线状态">
            <template slot-scope="scope">
              <div class="name-wrapper">
                {{ scope.row.isOnlin === 1 ? '在线' : '下线' }}
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div slot="footer" class="dialog-footer">
          <el-button @click="deleteVisible = false">取 消</el-button>
          <el-button type="danger" @click="sureDelete()">删 除</el-button>
        </div>
      </el-dialog>

      <!-- 增加用户表单弹窗
      <el-dialog title="增加管理员" :visible.sync="addPeopleVisible">
        <el-form :model="peopleForm">
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input v-model="peopleForm.monitorType" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话号码" :label-width="formLabelWidth">
            <el-input v-model="peopleForm.companyName" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addVisible = false">取 消</el-button>
          <el-button type="primary" @click="addPeole()">确 定</el-button>
        </div>
      </el-dialog> -->

      <!-- 删除管理员弹窗 -->
      <el-dialog title="确定要删除该用户吗？" :visible.sync="deletePeopleVisible">
        <el-table :data="theDeletePeople">
          <el-table-column property="userRealName" label="名称"></el-table-column>
          <el-table-column property="phone" label="电话号码"></el-table-column>
        </el-table>
        <div slot="footer" class="dialog-footer">
          <el-button @click="deleteVisible = false">取 消</el-button>
          <el-button type="danger" @click="sureDeletePeople()">删 除</el-button>
        </div>
      </el-dialog>

      <!-- 关联管理员弹窗 -->
      <el-dialog title="添加配电箱联系人管理" :visible.sync="relationVisible">
        <p class="checkboxTitle">选择绑定用户</p>
        <el-table
          ref="multipleTable"
          :data="isRelationform"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            prop="realName"
            label="姓名"
            width="120">
          </el-table-column>
          <el-table-column
            prop="phone"
            label="电话"
            show-overflow-tooltip>
          </el-table-column>
        </el-table>
          <el-pagination layout="prev, pager, next" :total="relationTotalPage" :page-size="8" @current-change="relationCurrentChange"></el-pagination>
        <div slot="footer" class="dialog-footer">
          <el-button @click="relationVisible = false">取 消</el-button>
          <el-button type="primary" @click="sureRelation()">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 回路信息列表 -->
      <el-dialog title="回路信息" :visible.sync="loopVisible">
        <el-button type="success" plain size="small" style= "float: right" @click="isAddLoop">增加回路</el-button>
        <el-table :data="loopData" ref="singleTable" highlight-current-row @current-change="loopCurrentChange">
          <el-table-column property="name" label="回路名称"></el-table-column>
          <el-table-column property="address" label="回路地址"></el-table-column>
          <el-table-column property="circuitNo" label="回路序号"></el-table-column>
          <el-table-column fixed="right" label="操作" width="100">
            <template slot-scope="scope">
              <el-button @click="updateLoop(scope.row)" type="text" size="small">编辑</el-button>
              <el-button type="text" size="small" @click="deleteLoop(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 增加与编辑回路弹窗 -->
        <el-dialog width="30%" :title="addAddupdataTitle" :visible.sync="addLoopVisible" append-to-body>
          <el-form :model="loopForm" :rules="loopRules" ref="loopForm">
            <el-form-item label="回路名称" :label-width="formLabelWidth" prop="name">
              <el-input v-model="loopForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="回路地址" :label-width="formLabelWidth" prop="address">
              <el-input v-model="loopForm.address" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="addLoopVisible = false">取 消</el-button>
            <el-button type="primary" @click="sureAddorUpdataLoop('loopForm')">{{loopOperation}}</el-button>
          </div>
        </el-dialog>
        <div slot="footer" class="dialog-footer">
          <el-button @click="outerVisible = false">退 出</el-button>
        </div>
      </el-dialog>
    </el-container>
  </div>
</template>

<script>
import Vue from 'Vue'
import _fetch from '../../util/fetch'
import mainTree from './components/mainTree/mainTree'
export default {
  data () {
    return {
      data: [],
      peopleTableData: [],
      peopleCurrentRow: null,
      electricBoxdata: [],
      addAndUpdataVisible: false,
      deleteVisible: false,
      theDeleteUser: [],
      deletePeopleVisible: false,
      relationVisible: false,
      theDeletePeople: [],
      formLabelWidth: '120px',
      updataForm: {},
      addPeopleVisible: false,
      peopleForm: {},
      form: {},
      zoom: 12,
      events: {
        init: (o) => {
        },
        'moveend': () => {
        },
        'zoomchange': () => {
        }
      },
      center: [113.39888, 23.03747],
      theWindow: {},
      marker: {},
      isRelationform: [],
      multipleSelection: [],
      equipmentTitle: '',
      equipmentType: '',
      equipmentBtType: '',
      activeElectricBoxId: '',
      loopVisible: false,
      loopData: [],
      addLoopVisible: false,
      loopForm: {},
      addAddupdataTitle: '',
      loopOperation: '',
      loopAddorupdata: '',
      mapMessage: {},
      monitorTotalPage: 0,
      showWindow: false,
      peopletotalPage: 0,
      boxRules: {
        name: [
          { required: true, message: '请输入活动名称', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '请输入活动名称', trigger: 'blur' }
        ],
        temperatureValue: [
          { required: true, message: '请输入活动名称', trigger: 'blur' }
        ]
      },
      activeOrgId: '',
      relationTotalPage: 0,
      loopRules: {
        name: [
          {required: true, message: '请输入活动名称', trigger: 'blur'},
          {min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ],
        address: [
          {required: true, message: '请输入活动名称', trigger: 'blur'},
          {min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    handleCurrentChangePeople (val) {
      this.peopleCurrentRow = val
    },
    handleCurrentChange (val) {
      this.currentRow = val
      this.updataForm = (val === null) ? {} : val
    },

    // 显示操作反馈信息
    feedbackMessage (message, type) {
      this.$message({
        showClose: true,
        message: message,
        type: type
      })
    },
    // 为数组对象添加删除指定元素的方法
    removeByValue (arr, val) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] === val) {
          arr.splice(i, 1)
          break
        }
      }
    },
    // 是否增加设备
    isAddEquipmen () {
      if (this.activeElectricBoxId !== '') {
        this.equipmentTitle = '增加设备'
        this.addAndUpdataVisible = true
        this.equipmentType = 1
        this.equipmentBtType = '确定'
        this.form = {}
      } else {
        this.feedbackMessage('请先选择要操作的电箱', 'warning')
      }
    },
    // 确定删除用户
    sureDelete () {
      this.deleteVisible = false
      // 返回删除设备id
      _fetch({
        url: '/device/delete',
        type: 'post',
        data: {
          id: this.currentRow.deviceId,
          electricBoxId: this.activeElectricBoxId
        }
      })
      .then((res) => {
        this.currentRow = null
        // 刷新设备表单
        _fetch({
          url: '/device/list',
          type: 'post',
          data: {
            electricBoxId: this.activeElectricBoxId,
            pageNo: 1,
            pageSize: 4
          }
        })
        .then((res) => {
          this.electricBoxdata = res.data.object
          this.addAndUpdataVisible = false
          this.form = {}
          this.monitorTotalPage = res.data.totalData
        })
      })
    },
    // 是否删除用户
    isDelete () {
      if (this.currentRow == null) {
        this.feedbackMessage('请先选择要操作的用户信息', 'warning')
      } else {
        this.deleteVisible = true
        Vue.set(this.theDeleteUser, 0, this.currentRow)
      }
    },
    // 是否修改用户
    isupdata () {
      if (this.currentRow == null) {
        this.feedbackMessage('请先选择要操作的用户信息', 'warning')
      } else {
        this.equipmentTitle = '修改设备信息'
        this.equipmentType = 2
        this.addAndUpdataVisible = true
        this.equipmentBtType = '修改'
        const theForm = Object.assign({}, this.currentRow)
        this.form = theForm
      }
    },
    // 确定增加或者修改设备
    addOrUpdata (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.equipmentType === 1) {
            // 返回新增设备数数据
            _fetch({
              url: '/device/add',
              type: 'post',
              data: {
                code: this.form.code,
                name: this.form.name,
                temperatureValue: this.form.temperatureValue,
                electricBoxId: this.activeElectricBoxId
              }
            })
            .then((res) => {
              // 刷新设备表单
              _fetch({
                url: '/device/list',
                type: 'post',
                data: {
                  electricBoxId: this.activeElectricBoxId,
                  pageNo: 1,
                  pageSize: 4
                }
              })
              .then((res) => {
                this.electricBoxdata = res.data.object
                this.addAndUpdataVisible = false
                this.form = {}
                this.monitorTotalPage = res.data.totalData
              })
            })
          }
          if (this.equipmentType === 2) {
            // 返回修改后的数据
            _fetch({
              url: '/device/update',
              type: 'post',
              data: {
                id: this.currentRow.deviceId,
                code: this.form.code,
                name: this.form.name,
                electricBoxId: this.activeElectricBoxId,
                temperatureValue: this.form.temperatureValue
              }
            })
            .then((res) => {
              // 请求数数据
              _fetch({
                url: '/device/list',
                type: 'post',
                data: {
                  electricBoxId: this.activeElectricBoxId,
                  pageNo: 1,
                  pageSize: 4
                }
              })
              .then((res) => {
                this.electricBoxdata = res.data.object
                this.addAndUpdataVisible = false
              })
            })
          }
        } else {
          this.$message.error('请完整地填好数据')
          return false
        }
      })
    },
    // 添加管理员
    addPeole () {
      // 发请求
      console.log(this.peopleForm)
      this.addPeopleVisible = false
    },

    // 删除管理员
    isDeletePeole () {
      if (this.peopleCurrentRow == null) {
        this.feedbackMessage('请先选择要操作的用户信息', 'warning')
      } else {
        this.deletePeopleVisible = true
        Vue.set(this.theDeletePeople, 0, this.peopleCurrentRow)
      }
    },
    // 确定删除管理员
    sureDeletePeople () {
      _fetch({
        url: 'http://10.21.23.126:8080/user/listByOrgByPage',
        type: 'post',
        data: {
          orgId: this.activeOrgId,
          page: 1,
          limit: 6
        }
      })
      .then((res) => {
        this.isRelationform = res.data.object
        this.relationTotalPage = res.data.totalData
      })
    },
    // 关联管理员
    isRelation () {
      if (this.activeElectricBoxId !== '') {
        this.relationVisible = true
        _fetch({
          url: 'http://10.21.23.126:8080/user/listByOrgByPage',
          type: 'post',
          data: {
            orgId: this.activeOrgId,
            page: 1,
            limit: 6
          }
        })
        .then((res) => {
          this.isRelationform = res.data.object
          this.relationTotalPage = res.data.totalData
        })
      } else {
        this.feedbackMessage('请先选择要操作的电箱', 'warning')
      }
    },
    // 关联分页管理
    relationCurrentChange (page) {
      _fetch({
        url: 'http://10.21.23.126:8080/user/listByOrgByPage',
        type: 'post',
        data: {
          orgId: this.activeOrgId,
          page: page,
          limit: 6
        }
      })
      .then((res) => {
        this.isRelationform = res.data.object
        this.relationTotalPage = res.data.totalData
      })
    },

    // 确定关联管理员
    sureRelation () {
      if (this.multipleSelection.length) {
        var userIdObj = []
        for (let i = this.multipleSelection.length, j = 0; j < i; j++) {
          let oneIdObj = {}
          oneIdObj.userId = this.multipleSelection[j].userId
          userIdObj.push(oneIdObj)
        }
        _fetch({
          url: '/electricBox/relateUser',
          type: 'post',
          data: {
            electricBoxId: this.activeElectricBoxId,
            userId: JSON.stringify(userIdObj)
          }
        })
        .then((res) => {
          // 刷新电箱管理员列表
          _fetch({
            url: '/electricBox/listRelatedUser',
            type: 'post',
            data: {
              electricBoxId: this.activeElectricBoxId,
              pageNo: 1,
              pageSize: 2
            }
          })
          .then((res) => {
            this.peopleTableData = res.data.object
            this.peopletotalPage = res.data.totalData
            this.relationVisible = false
          })
        })
      } else {
        this.$message.error('请先选择关联人员')
      }
    },

    // 管理员多选框
    handleSelectionChange (val) {
      this.multipleSelection = val
      console.log(val)
    },

    // 得到子组件设备信息并请求电箱管理员数据
    showElectricBox (data, id, totalData) {
      this.electricBoxdata = data
      this.activeElectricBoxId = id
      this.monitorTotalPage = totalData
      _fetch({
        url: '/electricBox/listRelatedUser',
        type: 'post',
        data: {
          electricBoxId: id,
          pageNo: 1,
          pageSize: 2
        }
      })
      .then((res) => {
        this.peopleTableData = res.data.object
        this.peopletotalPage = res.data.totalData
      })
    },
    // 查看回路信息
    showLoop () {
      if (this.currentRow == null) {
        this.feedbackMessage('请先选择要操作的用户信息', 'warning')
      } else {
        // 返回回路数据
        _fetch({
          url: '/circuit/list',
          type: 'post',
          data: {
            deviceId: this.currentRow.deviceId
          }
        })
        .then((res) => {
          this.loopData = res.data.object
          this.loopVisible = true
        })
      }
    },
    // 得到所选择的回路信息
    loopCurrentChange (val) {
      console.log(val)
    },
    // 是否增加回路
    isAddLoop () {
      this.loopAddorupdata = 1
      this.addAddupdataTitle = '增加回路'
      this.loopOperation = '确 定'
      this.addLoopVisible = true
    },
    // 确定增加回路
    sureAddorUpdataLoop (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.loopAddorupdata === 1) {
            // 返回增添回路的数据
            _fetch({
              url: '/circuit/add',
              type: 'post',
              data: {
                deviceId: this.currentRow.deviceId,
                name: this.loopForm.name,
                address: this.loopForm.address
              }
            })
            .then((res) => {
              _fetch({
                url: '/circuit/list',
                type: 'post',
                data: {
                  deviceId: this.currentRow.deviceId
                }
              })
              .then((res) => {
                this.loopData = res.data.object
                this.addLoopVisible = false
                this.loopForm = {}
              })
            })
          } else {
            // 返回修改回路的数据
            _fetch({
              url: '/circuit/update',
              type: 'post',
              data: {
                id: this.loopForm.id,
                name: this.loopForm.name,
                address: this.loopForm.address,
                deviceId: this.currentRow.deviceId
              }
            })
            .then((res) => {
              // 重新刷新回路信息
              _fetch({
                url: '/circuit/list',
                type: 'post',
                data: {
                  deviceId: this.currentRow.deviceId
                }
              })
              .then((res) => {
                this.loopData = res.data.object
                this.addLoopVisible = false
                this.loopForm = {}
              })
            })
          }
        } else {
          this.$message.error('请填写完整数据')
          return false
        }
      })
    },
    // 修改回路信息
    updateLoop (val) {
      this.addAddupdataTitle = '编辑回路'
      this.loopOperation = '修 改'
      this.loopAddorupdata = 2
      this.addLoopVisible = true
      let loopForm = Object.assign({}, val)
      this.loopForm = loopForm
    },
    // 删除回路信息
    deleteLoop (val) {
      this.$confirm('此操作将永久删除该回路, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 向服务器发送删除回路信息
        _fetch({
          url: '/circuit/delete',
          type: 'post',
          data: {
            id: val.id,
            deviceId: this.currentRow.deviceId
          }
        })
        .then((res) => {
          _fetch({
            url: '/circuit/list',
            type: 'post',
            data: {
              deviceId: this.currentRow.deviceId
            }
          })
          .then((res) => {
            this.loopData = res.data.object
            this.addLoopVisible = false
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 添加管理员分页
    managerCurrentChange (currentPage) {
      // 重新加载当前页数的管理员列表
      _fetch({
        url: '/electricBox/listRelatedUser',
        type: 'post',
        data: {
          electricBoxId: this.activeElectricBoxId,
          pageNo: currentPage,
          pageSize: 2
        }
      })
      .then((res) => {
        this.peopleTableData = res.data.object
        this.peopletotalPage = res.data.totalData
      })
    },
    // 拿到树组件传回来的电箱信息
    showElectricBoxdata (data, treeParentlebel) {
      if (data) {
        this.center = [data.longitude, data.latitude]
        this.marker.position = [data.longitude, data.latitude]
        this.theWindow.position = [data.longitude, data.latitude]
        this.mapMessage.name = data.name
        this.mapMessage.mechanism = treeParentlebel
        this.mapMessage.address = data.address
        this.activeOrgId = data.orgId
      }
    },
    // 页面改变时重新发请求
    monitorPageChange (pageNumber) {
      _fetch({
        url: '/device/list',
        type: 'post',
        data: {
          electricBoxId: this.activeElectricBoxId,
          pageNo: pageNumber,
          pageSize: 4
        }
      })
      .then((res) => {
        this.electricBoxdata = res.data.object
      })
    },
    // 关联列表时间的转换
    relationDate (date) {
      let time = new Date(date)
      let newTime = `${time.getFullYear()}-${time.getMonth() + 1}-${time.getDate()}`
      return newTime
    }
  },
  created () {
    // 请求数数据
    _fetch({
      url: '/dataTree/list?treeType=2',
      type: 'post',
      data: {
      }
    })
    .then((res) => {
      this.data = res.data.object
    })
  },
  mounted () {
    // 计算地图标记属性
    let marker = {}
    let theWindow = {}
    let self = this
    marker = {
      position: [113.39888, 23.03747],
      events: {
        click () {
          self.showWindow = false
          self.theWindow.visible = false
          self.$nextTick(() => {
            self.theWindow.visible = true
            self.showWindow = true
          })
        }
      }
    }

    theWindow = {
      position: [113.39888, 23.03747],
      visible: false
    }
    this.marker = marker
    this.theWindow = theWindow
  },
  components: {
    mainTree
  }
}
</script>

<style>
  .el-main {
    /* background-color: #E9EEF3; */
    /* color: #333; */
  }
  .treeContainer {
    position:absolute;
    top: 100px;
    bottom: 0;
    padding: 25px 35px 20px 25px;
  }
  .dataContainer {
    position: absolute;
    left: 380px;
    top: 100px;
    bottom: 0;
    right: 0;
    padding: 25px 32px 42px 0;
  }
  .leftTitle {
    font-size: 35px;
    margin-top: 0;
    margin-bottom: 0;
  }
  .leftSmallTitle {
    font-size: 18px;
    margin-top: 20px;
    margin-bottom: 0;
  }
  .monitorTitle {
    font-size: 18px;
    display: block;
    margin-right: 60px
  }
  .buttonArr {
    display: flex;
  }
  .buttonArr img {
    margin-right: 10px;
  }
  .operationButton {
    margin-right: 50px;
    cursor:pointer;
  }
  .operationstr {
    font-size: 13px;
  }
  .operationButtonpeole {
    margin-right: 30px;
    cursor:pointer;
  }
  .bottomLeft {
    position: absolute;
    bottom: 0;
    top: 350px;
  }
  .bottomRight {
    position: absolute;
    bottom: 0;
    top: 350px;
    left: 500px;
    right: 0;
    padding: 0 32px 0 0;
  }
  .amap-demo {
    height: 80%;
  }
  .amap-page-container {
    height: 80%;
  }
  .markerTitle {
    font-weight: bold;
    font-size: 15px
  }
  .checkboxTitle {
    font-size: 1px
  }
  .electricBoxRadio p {
    margin: 0 50px;
  }
  .pageCenter {
    display: flex;
    justify-content: center;
  }
</style>



