// import * as TYPES from './mutation-types'

export default {
  halo (state, payload) {
    state.halo += payload.halo
  },
  // 设置登录状态
  setLoginState (state, result) {
    console.log(result)
    state.isLogin = result
  },
  // 记录用户信息
  setUserInfo (state, userObj) {
    console.log('userObj', userObj)
    state.userObj = userObj
  }
}



