import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

let defaultState = {
  haha: 1,
  userObj: {}, // 当前登录的用户对象
  isLogin: false // 是否登录
}

export default new Vuex.Store({
  state: defaultState,
  mutations,
  actions,
  getters
})



