<template>
  <div class="login">
    <el-form ref="loginForm" :model="loginForm" label-width="80px" :rules="rules">
      <el-form-item label="账号" prop="username">
        <el-input v-model="loginForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="loginForm.password"></el-input>
      </el-form-item>
      <el-form-item class="operate">
          <el-button type="primary" @click="login">登录</el-button>
        </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import _fetch from '../../util/fetch'

export default {
  data () {
    return {
      // 登录的表单
      loginForm: {username: '', password: ''},
      // 表单的规则
      rules: {
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'},
          {min: 4, max: 20, message: '长度在4-20个字符', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 36, message: '长度在6-36个字符', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 登陆
    login () {
      this.$refs['loginForm'].validate((valid) => {
        // 如果输入合法的话
        if (valid) {
          // console.log('valid', valid)
          // 上线的时候要删掉这些代码
          // console.log('登录成功')
          // // 记录用户信息到vuex中
          // let userObj = {
          //   username: this.loginForm.username
          // }
          // this.setUserInfo(userObj)
          // this.setLoginState(true)
          // // 跳路由 这里要改为首页
          // this.$router.push('/')
          // 发送给后台账号密码
          _fetch({
            url: '/user/login',
            type: 'post',
            data: {
              username: this.loginForm.username,
              password: this.loginForm.password
            }
          })
            .then(res => {
              if (res) {
                console.log('登录成功')
                console.log('res', res)
                // 记录用户信息到vuex中
                let userObj = {
                  username: this.loginForm.username
                }
                this.setUserInfo(userObj)
                this.setLoginState(true)
                // 跳路由 这里要改为首页
                this.$router.push('/')
              }
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    ...mapMutations(['setLoginState', 'setUserInfo'])
  }
}
</script>

<style scoped>
.login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.operate {
  display: flex;
  justify-content: center;
}
</style>


