import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import map from '@/components/map'
import equipment from '@/components/equipment'
import user from '@/components/user'
import company from '@/components/company'
// import show from '@/components/company/show'
// import add from '@/components/company/add'
// import deleteCompany from '@/components/company/deleteCompany'
import monitor from '@/components/monitor'
import warning from '@/components/warning'
import group from '@/components/group'
import electricBox from '@/components/electricBox'
import store from '../store'
import login from '@/components/login/index'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/map',
      name: 'map',
      component: map
    },
    {
      path: '/equipment',
      name: 'equipement',
      component: equipment
    },
    {
      path: '/user',
      name: 'user',
      component: user
    },
    {
      path: '/warning',
      name: 'warning',
      component: warning
    },
    {
      path: '/company',
      name: 'company',
      component: company
      /* children: [{
        path: 'show',
        name: 'show',
        component: show
      },
      {
        path: 'add',
        name: 'add',
        component: add
      },
      {
        path: 'deleteCompany',
        name: 'deleteCompany',
        component: deleteCompany
      }] */
    },
    {
      path: '/monitor',
      name: 'monitor',
      component: monitor
    },
    {
      path: '/electricBox',
      name: 'electricBox',
      component: electricBox
    },
    {
      path: '/group',
      name: 'group',
      component: group
    }
  ]
})

router.beforeEach((to, from, next) => {
  // 如果要去的路径就是登录的页面
  if (to.path === '/login') {
    // 判断是否登录了，如果登录了，就返回首页
    if (store.state.isLogin) {
      next({path: '/'})
    } else {
      next()
    }
  } else {
    // 判断是否登录，没有就跳转回登录页面。还要再判断cookie，先看看后台怎么弄，cookie信息有什么
    if (store.state.isLogin) {
      next()
    } else {
      console.log('你还没有登陆!')
      next({path: '/login'}) // 失败则跳转到登陆页面
    }
  }
})

export default router



