<template>
  <div class="box">
    <div class="boxTitle"><span>实时监控</span></div>
    <div class="boxMiddle">
      <!-- 左边的树 -->
      <div class="middleLeft">
        <div class="middleLeftTitle">
          <span>用户列表</span>
          <input placeholder="点此搜索" v-model="searchText"/>
        </div>
        <el-tree :data="monitorTreeData" @node-click="handleNodeClick" :render-content="renderFunc" class="monitorTree" :filter-node-method="search" ref="tree"></el-tree>
      </div>
      <!-- 右边的设备列表 -->
      <div class="middleRight">
        <div class="middleRightTitle"><span>设备列表</span></div>
        <div class="monitorList">
          <div v-for="monitor in monitorDataList" :key="monitor.index" :class="monitor.isHint ? 'warning monitorBox' : 'monitorBox'">
            <div class="tableTitle">
              <div class="monitorName">
                <span>配电设备</span>
                <el-button class="historyBtn" @click="showHistoryDialog(monitor.deviceId)" size="mini">查看历史</el-button>
              </div>
              <div class="monitorId"><span>ID:{{ monitor.deviceCode }}</span></div>
            </div>
            <el-table :data="monitor.circuitDataLogs" class="roadTable" style="width: 100%">
              <el-table-column prop="circuitNo"  label="路数" min-width="50" :formatter="formatRoadNumber"></el-table-column>
              <el-table-column prop="currentValue" label="电流" min-width="50" :formatter="formatI"></el-table-column>
              <el-table-column prop="temperatureValue" label="温度" min-width="50" :formatter="formatTemperature"></el-table-column>
              <el-table-column prop="miliCurrentValue" label="漏电流" min-width="70" :formatter="formatI"></el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>

    <!-- 历史信息 -->
    <el-dialog title="历史信息" class="historyTable" :visible.sync="historyDialogTableVisible">
      <div class="historySelect">
        <!-- 日期 -->
        <div class="block">
          <span class="demonstration">查询日期</span>
          <el-date-picker
            v-model="historyTime"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="getHistoryTable">
          </el-date-picker>
        </div>
        <!-- 选择器 -->
        <el-select v-model="historySelectValue" placeholder="请选择要查询" @change="getHistoryTable">
          <el-option
            v-for="item in typeOption"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <el-table :data="historyTableData">
        <el-table-column property="serialNo" label="序列号"></el-table-column>
        <el-table-column property="deviceCode" label="设备编码"></el-table-column>
        <el-table-column property="circuitNo" label="回路序号"></el-table-column>
        <el-table-column property="circuitStatus" label="回路状态"></el-table-column>
        <el-table-column property="value" label="值"></el-table-column>
        <el-table-column property="date" label="创建时间"></el-table-column>
      </el-table>
      <!-- 分页 -->
      <!-- 分页的时候，需要发送 当前页 每页条数 需要拿到总页数 当前页的内容 -->
      <div class="page">
        <el-pagination
          @size-change="handleHistorySizeChange"
          @current-change="handleHistoryCurrentChange"
          :current-page="historyCurrentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="historyPageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="historyTotal">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import _fetch from '../../util/fetch'
  export default {
    data () {
      return {
        monitorTreeData: [], // 树形图
        searchText: '',
        deviceId: 2, // 记录设备id 记得弄为空
        monitorDataList: [], // 监控列表
        historyTableData: [], // 历史信息
        historyDialogTableVisible: false, // 显示历史信息的表示是否弹出
        historyTime: '', // 要的查询历史的时间范围
        typeOption: [
          {
            label: '电流',
            value: 201
          }, {
            label: '温度',
            value: 202
          }, {
            label: '漏电流',
            value: 203
          }
        ], // 历史查询的选项
        historySelectValue: '', // 历史查询的选项的值
        historyPageSize: 10, // 每页条数
        historyCurrentPage: 1, // 当前页。这个只是为了初始化而已
        historyTotal: 0, // 总页数
        base64_string: ''
      }
    },
    watch: {
      searchText (val) {
        this.$refs.tree.filter(val)
      }
    },
    methods: {
      // 请求树形图 设备 or 机构
      getMonitorTree () {
        _fetch({
          url: '/dataTree/list',
          type: 'post',
          data: {
            treeType: 2
          }
        })
        .then((res) => {
          // console.log('res', res)
          this.monitorTreeData = res.data.object
        })
      },
      // 点击树的时候
      handleNodeClick (data) {
        // console.log(data)
        // 判断是不是根节点 要渲染表格 是的话 data.children === undefined
        // 弹出表格
        // console.log('children', data.children)
        if (data.children === null) {
          // console.log('渲染表格')
          this.getMonitorTable(data.id)
        }
      },
      // 重新渲染树节点、让预警的字体变红
      renderFunc (h, { node, data, store }) {
        // console.log(data)
        // 判断是否报警。如果是，就添加背景色
        let isWarning = data.isHint
        if (isWarning === 1) {
          return (
            <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px; color: #fcdada; height: 29px;">
              <span>
                <span>{node.label}</span>
              </span>
            </span>
          )
        } else {
          return (
            <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px; height: 29px;">
              <span>
                <span>{node.label}</span>
              </span>
            </span>
          )
        }
      },
      // 树形节点内容搜索
      search (value, data, node) {
        if (!value) return true
        return data.label.indexOf(value) !== -1
      },
      // 获取设备列表
      getMonitorTable (electricBoxId) {
        // console.log('esleid', electricBoxId)
        _fetch({
          url: '/dataLog/list',
          type: 'post',
          data: {
            electricBoxId: electricBoxId
          }
        })
        .then(res => {
          this.monitorDataList = res.data.object
        })
      },
      // 格式化回路信息
      formatRoadNumber (row, column, cellValue) {
        return cellValue + '路'
      },
      formatI (row, column, cellValue) {
        if (!(cellValue === null)) {
          return cellValue + 'mA'
        }
      },
      formatTemperature (row, column, cellValue) {
        if (!(cellValue === null)) {
          return cellValue + '°C'
        }
      },
      // 弹出显示历史信息的表格
      showHistoryDialog (deviceId) {
        // 清空原来的表格内容 查询时间等。重置当前页
        this.historyTableData = []
        this.historyTime = ''
        this.historySelectValue = ''
        this.historyCurrentPage = 1
        this.historyPageSize = 10
        this.historyTotal = 0
        this.historyDialogTableVisible = true
      },
      // 获取历史信息的表格内容
      getHistoryTable () {
        // console.log('hisvalue', this.historySelectValue)
        // 判断是否选择了类型
        if (this.historySelectValue && this.historyTime) {
          _fetch({
            url: '/dataLog/historyList',
            type: 'post',
            data: {
              deviceId: this.deviceId,
              typeId: this.historySelectValue,
              pageNo: this.historyCurrentPage,
              pageSize: this.historyPageSize,
              startTime: this.historyTime[0],
              endTime: this.historyTime[1]
            }
          })
          .then(res => {
            // console.log('历史信息')
            // console.log(res)
            // console.log(res.data.object)
            // res.data.obj 这就是我要的数组data
            // 修改总页数
            this.historyTotal = res.data.totalData
            this.historyTableData = res.data.object.map((obj) => {
              switch (obj.circuitStatus) {
                case 101: {
                  obj.circuitStatus = '报警'
                  break
                }
                case 102: {
                  obj.circuitStatus = '故障'
                  break
                }
                case 103: {
                  obj.circuitStatus = '正常'
                  break
                }
                case 104: {
                  obj.circuitStatus = '未连接'
                  break
                }
                default: obj.circuitStatus = '状态出错'
              }
              let time = new Date(obj.date)
              obj.date = `${time.getFullYear()}-${time.getMonth() + 1}-${time.getDate()}`
              return obj
            })
          })
        }
      },
      // 分页
      // 当前页 改变的时候
      handleHistoryCurrentChange (historyCurrentPage) {
        this.historyCurrentPage = historyCurrentPage
        this.getHistoryTable()
      },
      // 每页条数改变的时候
      handleHistorySizeChange (historyPageSize) {
        this.historyPageSize = historyPageSize
        this.getHistoryTable()
      }
    },
    created: function () {
      this.getMonitorTree()
    }
  }
</script>

<style scoped>
.box {
  padding: 32px 0 0 40px;
}
.boxTitle {
  width: 100%;
  margin-bottom: 39px;
  font-size: 42px;
  font-weight: bold;
}
.boxMiddle {
  display: flex;
}
.middleLeft {
  margin-right: 35px;
}
.middleLeftTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-weight: bold;
}
.middleLeftTitle > span {
  font-size: 24px;
}
.middleLeftTitle > input {
  height: 14px;
  border: none;
  width: 70px;
  padding: 5px;
}
.monitorTree {
  width: 385px;
}

/* 右边。设备列表 */
.middleRight {
  flex-grow: 1;
  margin-right: 30px;
}
.middleRightTitle {
  margin-bottom: 24px;
  font-size: 24px;
  font-weight: bold;
}
.monitorList {
  background-color: #f1f1f1;
  padding: 24px 0 0 40px;
  display: flex;
  flex-wrap: wrap;
}
.monitorName {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px 0 0;
}
.monitorBox {
  width: 231px;
  height: 195px;
  background-color: white;
  margin: 0 52px 24px 0;
  color: black;
}
.tableTitle {
  padding: 0 0 0 10px;
  margin-top: 10px;
}
/* 历史表格 */
.historyTable .demonstration {
  margin-right: 20px;
}
.historyTable .block, .historySelect {
  margin-bottom: 20px;
}
/* 查看历史 */
.historySelect {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.historyTable .page {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
</style>

<style>
/* 修改组件库组件的默认样式 加那么多前缀是为了不污染别的组件 */
/* 字体 */
.monitorList .el-table__body tr>td, .monitorList .el-table td, .el-table th.is-leaf, .monitorList .el-table__empty-block {
  font-weight: 400;
  padding: 5px 0 0 0;
  border-bottom: none;
  font-size: 14px;
  z-index: 1;  /* 这个是为了遮盖住表格原来的底边框 */
}
/* all */
/* 默认 */
.el-table__body-wrapper {
  /* overflow: hidden; */
}
.monitorList .el-table--enable-row-hover .el-table__body tr:hover>td {
  background-color: white;
}
.monitorBox, .monitorBox .el-table thead, .monitorBox .el-table th, .monitorBox .el-table tr, .monitorBox .el-table td, .historyBtn, .monitorBox .el-table--fit, .monitorBox .el-table__empty-block {
  background-color: white;
  font-size: 14px;
  color: black;
  transition: background-color 1s, color .3s;
}
.historyBtn {
  color: black;
  /* font-size: 14px; */
  cursor: pointer;
  height: 30px;
}
.monitorBox {
  overflow: hidden!important;
}
/* 这是蓝色的 */
.monitorBox:hover, .monitorBox:hover .el-table thead, .monitorBox:hover .el-table th, .monitorBox:hover .el-table tr, .monitorBox:hover .el-table td, .monitorBox:hover .el-table--fit, .monitorBox:hover .el-table__empty-block, .monitorBox:hover .historyBtn{
  background-color: #409eff!important;
  transition: background-color 1s, color .3s;
  color: white;
  /* border-bottom: none; */
}

/* 警报 */
.monitorBox.warning, .monitorBox.warning .el-table thead, .monitorBox.warning .el-table th, .monitorBox.warning .el-table tr, .monitorBox.warning .el-table td, .monitorBox.warning .el-table--fit, .monitorBox.warning .el-table__empty-block, .monitorBox.warning .historyBtn {
  background-color: #f56c6c;
  animation: twinkle 3s linear 0.2s infinite alternate;
  color: white;
}
.monitorBox.warning .el-table__empty-text {
  color: white;
}
/* 警报的动画 */
/* border-color 是为了改按钮的边框 */
@keyframes twinkle {
  0% {
    background-color: #f56c6c;
  } 50% {
    background-color: #ffffff;
    border-color: #ffffff;
  } 100% {
    background-color: #f56c6c;
  }
}
</style>



