<template>
  <div class="company">
    <div class="sideMenu">
      <el-tree :data="data" :props="defaultProps"  @node-click="handleNodeClick" ></el-tree>
    </div>
    <div class="tableCompany">
      <div class="container">
        <div style="margin-bottom: 10px; float: left;">
          <el-button @click="dialogAddFormVisible = true,getProvince()"><i class="el-icon-circle-plus-outline"></i>添加</el-button>
          <el-button @click="editClick(), getProvince(), getAdmins()"><i class="el-icon-edit-outline"></i>编辑</el-button>
          <el-button type="danger" @click="removeSelection"><i class="el-icon-delete"></i>删除</el-button>
        </div>  
        <el-table ref="multipleTable" stripe :data="tableCompany" tooltip-effect="dark" @selection-change="selectionChange" style="width: 100%;">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column prop="name" label="机构名字" width="150" show-overflow-tooltip>
          </el-table-column>
          <el-table-column label="地址">
            <el-table-column prop="province" label="省份" width="80">
            </el-table-column>
            <el-table-column prop="city" label="城市" width="80">
            </el-table-column>
            <el-table-column prop="area" label="区" width="80">
            </el-table-column>
          </el-table-column>
          <el-table-column label="详细地址" prop="address" width="150" show-overflow-tooltip>  
          </el-table-column>
          <el-table-column prop="email" label="邮箱" width="140" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="phone" label="公司电话" width="100" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="admin" label="管理员名字" width="120" show-overflow-tooltip>
          </el-table-column>
          <el-table-column prop="description" label="描述" show-overflow-tooltip>
          </el-table-column>
        </el-table>  
        <el-dialog title="添加" custom-class="type" :visible.sync="dialogAddFormVisible" :close-on-click-modal="false" >  
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm" status-icon>
            <el-form-item label="机构信息" :label-width="formLabelWidth">   
            </el-form-item>
            <el-form-item label="机构名字" prop="orgName" :label-width="formLabelWidth">  
              <el-input v-model="addForm.orgName" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="地址" :label-width="formLabelWidth" required>  
              <el-form-item prop="province">
                <el-select v-model="addForm.province" placeholder="请选择省份" @change="getCity">
                  <el-option v-for="item in provinces" :label="item.province" :value="item.provinceId" :key="item.provinceId"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="city">
                <el-select v-model="addForm.city" placeholder="请选择城市" no-data-text="请先选择省份" @change="getArea">
                  <el-option v-for="item in cities" :label="item.city" :value="item.cityId" :key="item.cityId"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="area">
                <el-select v-model="addForm.area" placeholder="请选择地区" no-data-text="请先选择城市" @change="getAreaId">
                  <el-option v-for="item in areas" :label="item.area" :value="item.areaId" :key="item.areaId"></el-option>
                </el-select>
              </el-form-item>
            </el-form-item>
            <el-form-item label="详细地址" prop="address" :label-width="formLabelWidth">  
              <el-input v-model="addForm.address" auto-complete="off"></el-input> 
            </el-form-item>
            <el-form-item label="邮箱" prop="email" :label-width="formLabelWidth">  
              <el-input v-model="addForm.email" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="电话" prop="orgPhone" :label-width="formLabelWidth">  
              <el-input v-model="addForm.orgPhone" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="描述" prop="orgDescription" :label-width="formLabelWidth">  
              <el-input v-model="addForm.orgDescription" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="管理员信息" :label-width="formLabelWidth">   
            </el-form-item>
            <el-form-item label="登录名" prop="username" :label-width="formLabelWidth">  
              <el-input v-model="addForm.username" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="真实姓名" prop="realName" :label-width="formLabelWidth">  
              <el-input v-model="addForm.realName" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="密码" prop="password" :label-width="formLabelWidth">  
              <el-input type="password" v-model="addForm.password" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="确认密码" prop="repassword" :label-width="formLabelWidth">  
              <el-input type="password" v-model="addForm.repassword" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="电话" prop="userPhone" :label-width="formLabelWidth">  
              <el-input v-model="addForm.userPhone" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="qq号码" prop="qq" :label-width="formLabelWidth">  
              <el-input v-model="addForm.qq" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="描述" prop="userDescription" :label-width="formLabelWidth">  
              <el-input v-model="addForm.userDescription" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item>
              <el-button @click="cancelSubmit('addForm')">取消</el-button>  
              <el-button type="primary" @click.native="addClick()" :loading="addLoading">提交</el-button>  
            </el-form-item>
          </el-form>      
        </el-dialog>
        <el-dialog title="编辑" custom-class="type" :visible.sync="dialogEditFormVisible" :close-on-click-modal="false" >  
          <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm" status-icon>
            <el-form-item label="机构信息">
            </el-form-item>
            <el-form-item label="地址" :label-width="formLabelWidth" required>  
              <el-form-item prop="province">
                <el-select v-model="editForm.province" placeholder="请选择省份" @change="getCity">
                  <el-option v-for="item in provinces" :label="item.province" :value="item.provinceId" :key="item.provinceId"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="city">
                <el-select v-model="editForm.city" placeholder="请选择城市" no-data-text="请先选择省份" @change="getArea">
                  <el-option v-for="item in cities" :label="item.city" :value="item.cityId" :key="item.cityId"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="area">
                <el-select v-model="editForm.area" placeholder="请选择地区" no-data-text="请先选择城市" >
                  <el-option v-for="item in areas" :label="item.area" :value="item.areaId" :key="item.areaId"></el-option>
                </el-select>
              </el-form-item>
            </el-form-item>
            <el-form-item label="详细地址" prop="address" :label-width="formLabelWidth">  
              <el-input v-model="editForm.address" auto-complete="off"></el-input> 
            </el-form-item>
            <el-form-item label="邮箱" prop="email" :label-width="formLabelWidth">  
              <el-input v-model="editForm.email" auto-complete="off"></el-input>  
            </el-form-item>
            <el-form-item label="公司电话" prop="phone" :label-width="formLabelWidth">  
              <el-input v-model="editForm.phone" auto-complete="off"></el-input>  
            </el-form-item>  
            <el-form-item label="描述" prop="description" :label-width="formLabelWidth">  
              <el-input v-model="editForm.description" auto-complete="off"></el-input>  
            </el-form-item>  
            <el-form-item label="管理员信息" :label-width="formLabelWidth">
            </el-form-item>
            <el-form-item label="管理员" prop="admin" :label-width="formLabelWidth">
              <el-select v-model="editForm.admin" placeholder="请选择" @change="getAdminId" >
                <el-option v-for="item in admins" :label="item.username" :value="item.userId" :key="item.userId"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button @click="cancelSubmit('editForm')">取消</el-button>  
              <el-button type="primary" @click.native="editSave()" :loading="editLoading">提交</el-button>  
            </el-form-item>
          </el-form>      
        </el-dialog>
        <el-row style="margin: 20px 0" type="flex" justify="center">   
          <el-col :span="18" >  
            <el-pagination                  
                    @size-change="pageSizeChange"  
                    @current-change="currentPageChange"  
                    :current-page="currentPage"  
                    :page-sizes="[10, 20, 30, 50]"  
                    :page-size="pageSize"  
                    layout="total, sizes, prev, pager, next, jumper"  
                    :total="total">  
            </el-pagination>  
          </el-col>  
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import _fetch from '../../util/fetch'
export default {
  created: function () {
    var _self = this
    _self.currentPage = 1
    _self.treeNode = 3
    _fetch({
      url: 'http://10.21.23.141:8080/dataTree/list',
      type: 'post',
      data: {
        treeType: 1
      }
    })
    .then((res) => {
      _self.data = res.data.object
      _fetch({
        url: 'http://10.21.23.126:8080/org/list',
        type: 'post',
        data: {
          parentId: _self.treeNode,
          page: _self.currentPage,
          limit: _self.pageSize
        }
      })
      .then((res) => {
        _self.tableCompany = res.data.object.userVOS
        _self.total = res.data.object.totalPage
      })
      .catch((err) => {
        console.log(err)
      })
    })
    .catch((err) => {
      console.log(err)
    })
  },
  data () {
    var validatePass = (rule, value, callback) => {   // 验证两次密码是否一致
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.addForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var validataOrg = (rule, value, callback) => {  // 验证机构是否重复
      if (value === '') {
        callback(new Error('请输入机构名字'))
      } else {
        let params = {
          'orgName': value
        }
        _fetch({
          url: 'http://10.21.23.126:8080/org/checkDuplicateOrgName',
          type: 'post',
          data: {
            orgName: params.orgName
          }
        })
        .then((res) => {
          callback(res.success)
        })
        .catch((err) => {
          callback(err)
        })
      }
    }
    var validataNickName = (rule, value, callback) => {   // 验证用户名是否重复
      if (value === '') {
        callback(new Error('请输入登录名'))
      } else {
        let params = {
          'username': value
        }
        _fetch({
          url: 'http://10.21.23.126:8080/user/checkDuplicateUsername',
          type: 'post',
          data: {
            username: params.username
          }
        })
        .then((res) => {
          callback(res.success)
        })
        .catch((err) => {
          callback(err)
        })
      }
    }
    return {
      data: [],  // 保存当前树信息
      tableCompany: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      provinceId: 0,  // 保存编辑时当前省id
      cityId: 0,
      areaId: 0,
      orgId: 0,
      treeNode: 0,  // 保存当前点击的树节点
      admins: [],  // 保存下拉框中的机构员工信息
      provinces: [],  // 保存下拉框中的省信息
      cities: [],
      areas: [],
      multipleSelection: [],
      adminId: 0,  // 保存当前管理员id
      dialogAddFormVisible: false,
      dialogEditFormVisible: false,
      loading: false,
      editLoading: false,
      addLoading: false,
      editFormRules: {
        name: [
          { required: true, message: '请输入机构名字', trigger: 'blur' },
          { validator: validataOrg, message: '机构名字已存在', trigger: 'blur' }
        ],
        province: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        city: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        area: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入地址', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '请输入电话', trigger: 'blur' }
        ],
        admin: [
          { required: true, message: '请选择', trigger: 'change' }
        ]
      },
      addFormRules: {
        orgName: [
          { required: true, message: '请输入机构名字', trigger: 'blur' },
          { validator: validataOrg, message: '机构名字已存在', trigger: 'blur' }
        ],
        province: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        city: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        area: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }
        ],
        address: [
          { required: true, message: '请输入地址', trigger: 'blur' }
        ],
        orgPhone: [
          { required: true, message: '请输入机构电话', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入正确电话', trigger: 'blur,change' }
        ],
        username: [
          { required: true, message: '请输入管理员登录名', trigger: 'blur' },
          { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' },
          { validator: validataNickName, message: '登录名已存在', trigger: 'blur' }
        ],
        realName: [
          { required: true, message: '请输入管理员真实姓名', trigger: 'blur' },
          { min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入管理员密码', trigger: 'blur' },
          { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
        ],
        repassword: [
          { required: true, message: '请再次输入管理员密码', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        userPhone: [
          { required: true, message: '请输入管理员电话', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入正确电话', trigger: 'blur,change' }
        ]
      },
      editForm: {
        province: '',
        city: '',
        area: '',
        address: '',
        email: '',
        phone: '',
        description: '',
        admin: ''
      },
      addForm: {
        orgName: '',
        province: '',
        city: '',
        area: '',
        address: '',
        email: '',
        orgPhone: '',
        orgDescription: '',
        username: '',
        realName: '',
        password: '',
        repassword: '',
        userPhone: '',
        qq: '',
        userDescription: ''
      },
      formLabelWidth: '100px',
      currentPage: 1,
      pageSize: 10,
      total: 0
    }
  },
  methods: {
    handleNodeClick: function (data) {  // 点击树节点时加载相应信息
      var _self = this
      _self.treeNode = data.id
      _self.currentPage = 1
      _fetch({
        url: 'http://10.21.23.126:8080/org/list',
        type: 'post',
        data: {
          parentId: _self.treeNode,
          page: _self.currentPage,
          limit: _self.pageSize
        }
      })
      .then((res) => {
        _self.tableCompany = res.data.object
        _self.total = res.data.totalData
      })
      .catch((err) => {
        console.log(err)
      })
    },
    loadingData: function () {  // 操作后更新页面
      var _self = this
      _self.loading = true
      setTimeout(function () {
        _fetch({
          url: 'http://10.21.23.126:8080/org/list',
          type: 'post',
          data: {
            parentId: _self.treeNode,
            page: _self.currentPage,
            limit: _self.pageSize
          }
        })
        .then((res) => {
          _self.tableCompany = res.data.object
          _self.total = res.data.totalData
        })
        .catch((err) => {
          console.log(err)
        })
        console.info('加载数据成功')
        _self.loading = false
      }, 300)
    },
    addClick: function () {   // 添加操作
      var _self = this
      _self.$refs['addForm'].validate(function (valid) {  // 表单验证
        if (valid) {
          _fetch({
            url: 'http://10.21.23.126:8080/org/add',
            type: 'post',
            data: {
              orgName: _self.addForm.orgName,
              orgAreaId: _self.areaId,
              orgAddress: _self.addForm.address,
              orgEmail: _self.addForm.email,
              orgPhone: _self.addForm.orgPhone,
              orgDescription: _self.addForm.orgPhone,
              username: _self.addForm.username,
              userRealName: _self.addForm.realName,
              userPassword: _self.addForm.password,
              userRePassword: _self.addForm.repassword,
              userPhone: _self.addForm.userPhone,
              userQQ: _self.addForm.qq,
              userDescription: _self.addForm.userDescription
            }
          })
          .then((res) => {
            _self.dialogAddFormVisible = false
            _self.$refs['addForm'].resetFields()
            _self.$message({
              message: '成功',
              type: 'success'
            })
            _self.loadingData()
          })
          .catch((err) => {
            console.log(err)
          })
        } else {
          _self.$message({
            message: '请输入正确信息',
            type: 'error'
          })
          return false
        }
      })
    },
    selectionChange: function (val) {
      this.multipleSelection = val
      console.info(val)
    },
    editClick: function () {  // 点击编辑按钮时
      var _self = this
      var multipleSelection = this.multipleSelection
      if (multipleSelection.length > 1 || multipleSelection.length < 1) {  // 选择信息条数
        _self.$message({
          message: '请至多选中一条记录',
          type: 'error'
        })
        return
      }
      var row = multipleSelection[0]
      _self.dialogEditFormVisible = true
      _self.editForm = Object.assign({}, row)  // 将所选机构信息显示到表单
      _self.orgId = row.orgId
      _self.adminId = row.managerId
      _fetch({    // 获取机构详细信息
        url: 'http://10.21.23.126:8080/org/getOrgEditInfo',
        type: 'post',
        data: {
          orgId: _self.orgId
        }
      })
      .then((res) => {
        _self.areaId = res.data.object.areaId
        _self.cityId = res.data.object.cityId
        _self.provinceId = res.data.object.provinceId
        _fetch({  // 获取机构所在省的所有市
          url: 'http://10.21.23.126:8080/provinceCityArea//getCitiesByProvince',
          type: 'post',
          data: {
            provinceId: _self.provinceId
          }
        })
        .then((res) => {
          _self.cities = res.data.object
        })
        .catch((err) => {
          console.log(err)
        })
        _fetch({  // 获取机构所在市所有区
          url: 'http://10.21.23.126:8080/provinceCityArea/getAreasByCity',
          type: 'post',
          data: {
            cityId: _self.cityId
          }
        })
        .then((res) => {
          _self.areas = res.data.object
        })
        .catch((err) => {
          console.log(err)
        })
        _fetch({  // 获取机构所在区所有机构
          url: 'http://10.21.23.126:8080/org/listByAreaId',
          type: 'post',
          data: {
            areaId: _self.areaId
          }
        })
        .then((res) => {
          _self.orgs = res.data.object
        })
        .catch((err) => {
          console.log(err)
        })
      })
      .catch((err) => {
        console.log(err)
      })
    },
    editSave: function () {   // 保存编辑信息
      var _self = this
      this.$refs['editForm'].validate(function (valid) {  // 表单验证
        if (valid) {
          _fetch({
            url: 'http://10.21.23.126:8080/org/update',
            type: 'post',
            data: {
              orgId: _self.orgId,
              areaId: _self.areaId,
              address: _self.editForm.address,
              email: _self.editForm.email,
              phone: _self.editForm.phone,
              description: _self.editForm.description,
              managerId: _self.adminId
            }
          })
          .then((res) => {
            _self.dialogEditFormVisible = false
            _self.$refs['editForm'].resetFields()
            _self.loadingData()
            _self.$message({
              message: '成功',
              type: 'success'
            })
          })
          .catch((err) => {
            console.log(err)
          })
        } else {
          _self.$message({
            message: '请输入正确信息',
            type: 'error'
          })
          return false
        }
      })
    },
    removeSelection: function () {
      var _self = this
      var multipleSelection = this.multipleSelection
      if (multipleSelection.length < 1) {
        _self.$message({
          message: '请至少选中一条记录',
          type: 'error'
        })
        return
      }
      var ids = ''
      var id = []
      for (var i = 0; i < multipleSelection.length; i++) {
        var row = multipleSelection[i]
        ids += row.name + ' '
        id[i] = row.orgId
      }
      this.$confirm('确认删除 ' + ids + '吗?', '提示', {
        type: 'warning'
      }).then(function () {
        _fetch({
          url: 'http://10.21.23.126:8080/org/delete',
          type: 'post',
          data: {
            ids: id
          }
        })
        .then((res) => {
          _self.loadingData()
          _self.$message({
            message: ids + '删除成功',
            type: 'success'
          })
        })
        .catch((err) => {
          console.log(err)
        })
      }).catch(function (e) {
        if (e !== 'cancel') {
          console.log('出现错误：' + e)
        }
      })
    },
    getAdmins: function () {  // 获取该机构所有员工
      var _self = this
      _fetch({
        url: 'http://10.21.23.126:8080/user/listByOrg',
        type: 'post',
        data: {
          orgId: _self.orgId
        }
      })
      .then((res) => {
        _self.admins = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getAdminId: function (val) {   // 记录管理员id
      var _self = this
      _self.adminId = val
    },
    getProvince: function () {    // 获取所有省
      var _self = this
      _fetch({
        url: 'http://10.21.23.126:8080/provinceCityArea/getAllProvinces',
        type: 'post'
      })
      .then((res) => {
        _self.provinces = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getCity: function (val) {
      var _self = this
      _self.editForm.city = ''
      _self.editForm.area = ''
      _self.addForm.city = ''
      _self.addForm.area = ''
      _fetch({
        url: 'http://10.21.23.126:8080/provinceCityArea/getCitiesByProvince',
        type: 'post',
        data: {
          provinceId: val
        }
      })
      .then((res) => {
        _self.cities = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getArea: function (val) {
      var _self = this
      _self.editForm.area = ''
      _self.addForm.area = ''
      _fetch({
        url: 'http://10.21.23.126:8080/provinceCityArea/getAreasByCity',
        type: 'post',
        data: {
          cityId: val
        }
      })
      .then((res) => {
        _self.areas = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getAreaId: function (val) {   // 记录所选区id
      var _self = this
      _self.areaId = val
    },
    pageSizeChange: function (val) {
      var _self = this
      _self.pageSize = val
      _self.loadingData()// 重新加载数据
    },
    currentPageChange: function (val) {
      var _self = this
      _self.currentPage = val
      _self.loadingData()// 重新加载数据
    },
    cancelSubmit: function (form, val) {
      var _self = this
      _self.$refs[form].resetFields()
      _self.dialogAddFormVisible = false
      _self.dialogEditFormVisible = false
      _self.cities = []
      _self.areas = []
    }
  }
}
</script>

<style>
.sideMenu {
  width: 15%;
  float: left;
  margin-top: 5px;
}
.tableCompany {
  width: 85%;
  float: left;
}
.container {
  margin: 5px;
}
</style>



