<template>
  <div class="box">
    <div class="boxTitle"><span>预警管理</span></div>
    <el-container>
      <el-aside width="385px">
        <div class="middleLeft">
        <div class="middleLeftTitle">
          <span>用户列表</span>
          <input placeholder="点此搜索" v-model="searchText"/>
        </div>
        <!-- <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="设备" name="first"></el-tab-pane>
          <el-tab-pane label="机构" name="second"></el-tab-pane>
        </el-tabs> -->
        <el-tree :data="warningTreeData" @node-click="handleNodeClick" :render-content="renderFunc" class="warningTree" :filter-node-method="search" ref="tree"></el-tree>
      </div>
      </el-aside>
      <el-main>
        <div class="middleRight">
        <!-- <div class="middleRightTitle"><span>设备列表</span></div> -->
        
        <!-- 操作栏 -->
        <div class="operate">
          <el-button @click="wantEdit" type="primary">修改</el-button>
        </div>
        <!-- 这里放表格 -->
        <el-table
          class="warningTable"
          ref="singleTable"
          :data="warningTableData"
          style="width: 100%"
          highlight-current-row
          @current-change="handleCurrentTableChange">
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column
            label="报警设备编号"
            prop="deviceCode">
          </el-table-column>
          <el-table-column
            prop="circuitNo"
            label="回路序号">
          </el-table-column>
          <el-table-column
            prop="event_code"
            label="报警事件编号">
          </el-table-column>
          <el-table-column
            prop="serialNo"
            label="信息序列号">
          </el-table-column>
          <el-table-column
            prop="typeLabel"
            label="处理状态">
          </el-table-column>
          <el-table-column
            prop="date"
            label="报警时间">
          </el-table-column>
        </el-table>

        <!-- 弹出框 -->
        <el-dialog title="设备" :visible.sync="dialogFormVisible">
          <el-form :model="form" class="selectForm">
            <el-form-item label="处理状态"></el-form-item>
            <el-select v-model="warningOperateValue" placeholder="请选择">
              <el-option
                v-for="item in warningOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="commit">确 定</el-button>
          </div>
        </el-dialog>

        <!-- 分页 -->
        <!-- 分页的时候，需要发送 当前页 每页条数 总页数 -->
        <div class="page">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import _fetch from '@/util/fetch'
  export default {
    data () {
      return {
        warningTreeData: [],
        activeName: 'first', // 默认标签页
        searchText: '',   // 树形图查找的内容
        electricBoxId: '',  // 记录电箱的id
        warningTableData: [],    // 表格的数据
        currentRow: null, // 单选。记录当前选中的值
        multipleSelection: [],    // 储存多选的
        dialogFormVisible: false,   // 判断dialog是否可见
        // dialog出来的表单的默认数据
        form: {
          name: ''
        },
        formLabelWidth: '120px',
        isEdit: false,   // 点击的是修改
        pageSize: 10,   // 每页页数
        currentPage: 1,   // 当前页。这个只是为了初始化而已
        total: 0,  // 总页数
        warningOptions: [
          {
            value: '0',
            label: '未解决'
          },
          {
            value: '1',
            label: '忽略'
          },
          {
            value: '2',
            label: '误报'
          },
          {
            value: '3',
            label: '已解决'
          }
        ], // 报警的处理状态
        warningOperateValue: ''
      }
    },
    watch: {
      searchText (val) {
        this.$refs.tree.filter(val)
      }
    },
    methods: {
      // 树形节点内容搜索
      search (value, data, node) {
        if (!value) return true
        return data.label.indexOf(value) !== -1
      },
      // 标签页
      // handleClick (tab, event) {
      //   console.log('tab', tab, event)
      //   console.log('index', tab.index)
      //   this.getWarningTree(tab.index)
      // },
      // 请求树形图 设备 or 机构
      getWarningTree (type) {
        _fetch({
          url: '/dataTree/list',
          type: 'post',
          data: {
            treeType: 2
          }
        })
        .then(res => {
          this.warningTreeData = res.data.object
        })
      },
      // 点击树的时候
      handleNodeClick (data) {
        // console.log(data)
        // 判断是不是根节点 要渲染表格 是的话 data.children === undefined
        // 弹出表格
        // console.log('children', data.children)
        if (data.children === null) {
          // console.log('渲染表格')
          // this.getWarningTable(id)
          // this.electricBoxId = data.electricBoxId
          // console.log('el id', data.id)
          this.electricBoxId = data.id
          this.getWarningTable()
        }
      },
      // 重新渲染树节点、让预警的字体变红
      renderFunc (h, { node, data, store }) {
        // console.log(data)
        // 判断是否报警。如果是，就添加背景色
        let isWarning = data.isHint
        if (isWarning === 1) {
          return (
            <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px; color: #fcdada; height: 29px;">
              <span>
                <span>{node.label}</span>
              </span>
            </span>
          )
        } else {
          return (
            <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px; height: 29px;">
              <span>
                <span>{node.label}</span>
              </span>
            </span>
          )
        }
      },
      // 请求后台预警设备的表格
      getWarningTable () {
        _fetch({
          url: '/deviceEvent/list',
          type: 'post',
          data: {
            pageNo: this.currentPage,
            pageSize: this.pageSize,
            electricBoxId: this.electricBoxId
          }
        })
        .then(res => {
          // console.log(res)
          // this.total = res.data.total
          // this.warningTableData = res.data.object
          this.total = res.data.totalData
          this.warningTableData = res.data.object.map((obj) => {
            switch (obj.event_code) {
              case 201: {
                obj.event_code = '电流超阈值'
                break
              }
              case 202: {
                obj.event_code = '温度超阈值'
                break
              }
              case 203: {
                obj.event_code = '漏电流超阈值'
                break
              }
              default: obj.event_code = '数据出错'
            }
            switch (obj.type) {
              case 0: {
                obj.typeLabel = '未解决'
                break
              }
              case 1: {
                obj.typeLabel = '忽略'
                break
              }
              case 2: {
                obj.typeLabel = '误报'
                break
              }
              case 3: {
                obj.typeLabel = '已解决'
                break
              }
              default: obj.typeLabel = '出错啦'
            }
            let time = new Date(obj.date)
            obj.date = `${time.getFullYear()}-${time.getMonth() + 1}-${time.getDate()}`
            return obj
          })
        })
        // 发送请求 成功后。所以到时候，多少条，就传对应的pageSize就好啦 当前页就好啦
      },
      // 单选
      setCurrent (row) {
        // console.log('row', row)
        this.$refs.singleTable.setCurrentRow(row)
      },
      handleCurrentTableChange (val) {
        // console.log('单选', val)
        this.currentRow = val
      },
      // 修改
      wantEdit () {
        // 判断是否只选中一个
        // console.log('currentRow', this.currentRow)
        if (this.currentRow === null) {
          this.$message('请选中一个')
        } else {
          // 修改置为true
          this.isEdit = true
          // 弹出修改的框
          this.form = this.currentRow
          this.dialogFormVisible = true
          this.warningOperateValue = this.form.typeLabel
          // 重新渲染表格
          this.getWarningTable()
        }
      },
      commit () {
        this.dialogFormVisible = false
        // 重置为false
        this.isEdit = false
        // 发送
        _fetch({
          url: '/deviceEvent/updateType',
          type: 'post',
          data: {
            id: this.form.id,
            type: this.warningOperateValue
          }
        })
        .then(res => {
          // 到这就是成功了
          // console.log('edit', res)
          this.$message({
            type: 'success',
            message: '修改成功'
          })
          // 刷新
          this.getWarningTable()
        })
      },
      // 取消提交表单
      cancel () {
        this.dialogFormVisible = false
        this.isEdit = false
      },
      // 当前页 改变的时候
      handleCurrentChange (currentPage) {
        this.currentPage = currentPage
        this.getWarningTable()
      },
      // 每页条数改变的时候
      handleSizeChange (pageSize) {
        this.pageSize = pageSize
        this.getWarningTable()
      }
    },
    created: function () {
      this.getWarningTree()
    }
  }
</script>

<style scoped>
.box {
  padding: 32px 0 0 40px;
}
.boxTitle {
  width: 100%;
  margin-bottom: 39px;
  font-size: 42px;
  font-weight: bold;
}
.middleLeftTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-weight: bold;
}
.middleLeftTitle > span {
  font-size: 24px;
}
.middleLeftTitle > input {
  height: 14px;
  border: none;
  width: 70px;
  padding: 5px;
}
.warningTree {
  width: 100%;
}

/* 右边。设备列表 */
.middleRightTitle {
  margin-bottom: 24px;
  font-size: 24px;
  font-weight: bold;
}
.warningList {
  background-color: #f1f1f1;
  padding: 24px 0 0 40px;
  display: flex;
  flex-wrap: wrap;
}
.warningList > div {
  width: 231px;
  height: 195px;
  background-color: #409eff;
  margin: 0 52px 52px 0;
  color: white;
}
/* 右侧表格 */
.tableTitle {
  padding: 0 0 0 10px;
  margin-top: 15px;
}
.middleRight {
  margin-left: 20px;
  margin-right: 20px;
}
.warningTable {
  margin: 40px 0 40px 0;
  width: 100%;
}
/* 分页 */
.page {
  display: flex;
  justify-content: center;
  align-items: center;
}
.selectForm {
  display: flex;
  justify-content: center;
}
</style>
<style>

/* 这个是为了修改组件的默认样式 */
.el-main {
  padding: 0;
}
.roadTable {
  background-color: #409eff;
  color: white;
}
.roadTable td, .el-table th.is-leaf {
  border-bottom: none;
}
.roadTable th, .roadTable tr, .roadTable td {
  background-color: #409eff;
  color: white;
  font-weight: 400;
  padding: 9px 0 0 0;
}
.roadTable, .roadTable th.is-leaf {
  border-bottom: none;
}
.roadTable .el-table--enable-row-hover .el-table__body tr:hover>td {
  background-color: black;
}
</style>



