import axios from 'axios'
// import host from '@/api'
// import * as statusCode from '../api/statusCode'
//
// ele.$notify 用于提示
import ele from '../main'

const instance = axios.create({
  withCredentials: true,    // 开启这个配置项才可以接受服务器端发送的cookies
  baseURL: 'http://10.21.23.141:8080',   // 用于本地调试，要跨域访问后台接口的话可以在这里通过引入config配置host和端口
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
})

const methodList = ['post', 'put', 'patch']

/**
 *
 * @param {传入的参数}
 * 用于将参数从对象转化成qs
 */
const stringifyData = (data) => {
  let res = []
  Object.keys(data).forEach((key) => {
    if (data[key] === null || data[key] === undefined) {
      // 呵呵
    } else {
      res.push(encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
    }
  })
  return res.join('&')
}

/**
 * [_fetch 用于http请求]
 * @param  {[type]} option [传入配置参数，用法类似 $.ajax]
 * @return {[type]}        [返回promise对象，用then方法获取返回的数据]
 *
 * 用法：
 * import _fetch from '../util/fetch'
 *
 * _fetch({
 *   url: '后台接口',
 *   type: 'post',
 *   data: {
 *     aid: 'edV03139DEaa',
 *     b: 1,
 *     c: 'hhh'
 *   }
 * })
 * .then((res) => {
 *   console.log(res.data)
 * })
 * .catch((err) => {
 *   // 捕获错误(可选)
 * })
 */
const _fetch = (option) => {
  if (!option.url) {
    console.error('使用_fetch时请先填入url')
    return
  }

  let requestPromise

  methodList.forEach((method) => {
    if (option.type && option.type.toLowerCase() === method) {
      const finalData = option.data ? stringifyData(option.data) : {}
      requestPromise = instance.request({
        url: option.url,
        method: option.type.toLowerCase(),
        data: finalData
      })
    }
  })

  if (!requestPromise) {
    let type = option.type || 'get'
    type = type.toLowerCase()
    requestPromise = instance.request({
      url: option.url,
      method: type,
      params: option.data || {}
    })
  }

  return requestPromise.then((res) => {
    if (res.data.success) {
      return res
    } else {
      ele.$message.error({
        title: '警告',
        message: res.data.message
      })
    }
  })
}

export default _fetch



