<template>
  <div class="user">
    <div style="margin: 10px 0 10px 15px; float: left;">
      <el-button @click="dialogFormVisible = true, type= 'add', editFormTitle = '添加', getProvince()"><i class="el-icon-circle-plus-outline"></i>添加用户</el-button>
      <el-button @click="editClick(), getProvince()"><i class="el-icon-edit-outline"></i>编辑用户</el-button>
      <el-button type="danger" @click="removeSelection(tableUser)"><i class="el-icon-delete"></i>删除用户</el-button>
    </div>  
    <el-table ref="multipleTable" stripe :data="tableUser" tooltip-effect="dark" @selection-change="selectionChange" style="width: 100%;">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column prop="username" label="登录名" width="180" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="realName" label="真实姓名" width="180" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="orgName" label="机构名称" width="180" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="phone" label="电话号码" width="180" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="qq" label="qq" width="180" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="description" label="描述" show-overflow-tooltip>
      </el-table-column>
    </el-table>
    <el-dialog :title="editFormTitle" custom-class="type" :visible.sync="dialogFormVisible" :close-on-click-modal="false" @close="cancelSubmit()" >  
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm" status-icon>
        <el-form-item label="登录名" prop="username" v-if="type==='add'">  
          <el-input v-model="editForm.username" auto-complete="off"></el-input>  
        </el-form-item>
        <el-form-item label="姓名" prop="realName">  
          <el-input v-model="editForm.realName" auto-complete="off"></el-input>  
        </el-form-item>
        <el-form-item label="密码" prop="password">  
          <el-input type="password" placeholder="若要修改密码请输入新密码" v-model="editForm.password" auto-complete="off"></el-input>  
        </el-form-item>
        <el-form-item label="确认密码" prop="repassword">  
          <el-input type="password" placeholder="请再次输入新密码" v-model="editForm.repassword" auto-complete="off"></el-input>  
        </el-form-item>
        <el-form-item label="机构" required>
          <el-form-item prop="province">
            <el-select v-model="editForm.province" placeholder="请选择省份" @change="getCity">
              <el-option v-for="item in provinces" :label="item.province" :value="item.provinceId" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="city">
            <el-select v-model="editForm.city" placeholder="请选择城市" no-data-text="请先选择省份" @change="getArea">
              <el-option v-for="item in cities" :label="item.city" :value="item.cityId" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="area">
            <el-select v-model="editForm.area" placeholder="请选择地区" no-data-text="请先选择城市" @change="getOrg">
              <el-option v-for="item in areas" :label="item.area" :value="item.areaId" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="orgName">
            <el-select v-model="editForm.orgName" placeholder="请选择机构" no-data-text="无机构" @change="getOrgId">
              <el-option v-for="item in orgs" :label="item.name" :value="item.orgId" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form-item>
        <el-form-item label="电话" prop="phone">  
          <el-input v-model="editForm.phone" auto-complete="off"></el-input> 
        </el-form-item>  
        <el-form-item label="qq" prop="qq">  
          <el-input v-model="editForm.qq" auto-complete="off"></el-input>  
        </el-form-item>  
        <el-form-item label="描述" prop="description">  
          <el-input v-model="editForm.description" auto-complete="off"></el-input>  
        </el-form-item>  
        <el-form-item>
            <el-button @click="cancelSubmit()">取消</el-button>  
            <el-button type="primary" @click.native="addClick()" :loading="editLoading">提交</el-button>  
        </el-form-item>
      </el-form>      
    </el-dialog>
    <el-row style="margin: 30px 0" type="flex" justify="center">   
      <el-col :span="18" >  
        <el-pagination                  
                @size-change="pageSizeChange"  
                @current-change="currentPageChange"  
                :current-page="currentPage"  
                :page-sizes="[10, 20, 30, 50]"  
                :page-size="pageSize"  
                layout="total, sizes, prev, pager, next, jumper"  
                :total="total">  
        </el-pagination>  
      </el-col>  
    </el-row>  
  </div>
</template>

<script>
import _fetch from '../../util/fetch'
export default {
  created: function () {
    var _self = this
    _self.currentPage = 1
    _fetch({
      url: 'http://10.21.23.126:8080/user/list',
      type: 'post',
      data: {
        page: _self.currentPage,
        limit: _self.pageSize
      }
    })
    .then((res) => {
      _self.tableUser = res.data.object
      _self.total = res.data.totalData
    })
    .catch((err) => {
      console.log(err)
    })
  },
  data () {
    var validatePass = (rule, value, callback) => {  // 验证两次密码是否一致
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.editForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var validataNickName = (rule, value, callback) => { // 验证用户名是否存在
      if (value === '') {
        callback(new Error('请输入登录名'))
      } else {
        let params = {
          'username': value
        }
        _fetch({
          url: 'http://10.21.23.126:8080/user/checkDuplicateUsername',
          type: 'post',
          data: {
            username: params.username
          }
        })
        .then((res) => {
          callback(res.success)
        })
        .catch((err) => {
          callback(err)
        })
      }
    }
    return {
      tableUser: [],
      editFormTitle: '添加',  // 模态框标题
      id: 0,  // 保存当前编辑的userId
      provinces: [], // 保存下拉框中的省信息
      cities: [],  // 保存下拉框中的市信息
      areas: [],  // 保存下拉框中的区信息
      orgs: [],  // 保存下拉框中的机构信息
      provinceId: 0,  // 保存编辑时当前省id
      cityId: 0,  // 保存编辑时当前市id
      areaId: 0,  // 保存编辑时当前区id
      orgId: 0,  // 保存编辑时当前机构id
      type: 'add',  // 识别当前操作为添加还是编辑
      multipleSelection: [],
      dialogFormVisible: false,
      loading: false,
      deleteVisible: false,
      editFormVisible: false,
      editLoading: false,
      editFormRules: {
        username: [
          { required: true, message: '请输入登录名', trigger: 'blur' },
          { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' },
          { validator: validataNickName, message: '登录名已存在', trigger: 'blur' }
        ],
        realName: [
          { required: true, message: '请输入真实姓名', trigger: 'blur' },
          { min: 0, max: 10, message: '长度在 0 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
        ],
        repassword: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        province: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        orgName: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        area: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        city: [
          { required: true, message: '请选择', trigger: 'change' }
        ],
        phone: [
          { required: true, message: '请输入电话', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请输入正确电话', trigger: 'blur,change' }
        ]
      },
      editForm: {
        username: '',
        realName: '',
        password: '',
        repassword: '',
        province: '',
        city: '',
        area: '',
        orgName: '',
        phone: '',
        qq: '',
        description: ''
      },
      formLabelWidth: '120px',
      currentPage: 1,  // 当前页面
      pageSize: 10,  // 每页显示条数
      total: 0  // 保存总共有多少条信息
    }
  },

  methods: {
    loadingData: function () {  // 操作后刷新页面再次加载数据
      var _self = this
      _self.loading = true
      setTimeout(function () {
        _fetch({
          url: 'http://10.21.23.126:8080/user/list',
          type: 'post',
          data: {
            page: _self.currentPage,
            limit: _self.pageSize
          }
        })
        .then((res) => {
          _self.tableUser = res.data.object
          _self.total = res.data.totalData
          _self.loading = false
        })
        .catch((err) => {
          console.log(err)
        })
      }, 300)
    },
    addClick: function () {  // 提交信息
      var _self = this
      _self.$refs['editForm'].validate(function (valid) {  // 表单验证
        if (valid) {
          if (_self.type === 'add') {  // 当为添加操作时
            _fetch({
              url: 'http://10.21.23.126:8080/user/add',
              type: 'post',
              data: {
                username: _self.editForm.username,
                realName: _self.editForm.realName,
                password: _self.editForm.password,
                rePassword: _self.editForm.repassword,
                orgId: _self.orgId,
                phone: _self.editForm.phone,
                qq: _self.editForm.qq,
                description: _self.editForm.description
              }
            })
            .then((res) => {
              _self.dialogFormVisible = false
              _self.$refs['editForm'].resetFields()  // 清空表单
              _self.$message({
                message: '添加成功',
                type: 'success'
              })
              _self.loadingData()
            })
            .catch((err) => {
              _self.$message({
                message: '添加失败',
                type: 'error'
              })
              console.log(err)
            })
          } else {  // 当为编辑操作时
            _fetch({
              url: 'http://10.21.23.126:8080/user/update',
              type: 'post',
              data: {
                userId: _self.id,
                username: _self.editForm.userame,
                realName: _self.editForm.realName,
                password: _self.editForm.password,
                repassword: _self.editForm.repassword,
                orgId: _self.orgId,
                phone: _self.editForm.phone,
                qq: _self.editForm.qq,
                description: _self.editForm.description
              }
            })
            .then((res) => {
              _self.$refs['editForm'].resetFields()
              _self.dialogFormVisible = false
              _self.$message({
                message: '更新成功',
                type: 'success'
              })
              _self.loadingData()
            })
            .catch((err) => {
              _self.$message({
                message: '更新失败',
                type: 'error'
              })
              console.log(err)
            })
          }
        } else {
          _self.$message({
            message: '请补全内容',
            type: 'error'
          })
        }
      })
    },
    selectionChange: function (val) {
      this.multipleSelection = val
      console.info(val)
    },
    editClick: function () {  // 点击编辑按钮时触发
      var _self = this
      _self.editFormTitle = '编辑'
      _self.type = 'edit'
      var multipleSelection = this.multipleSelection
      if (multipleSelection.length > 1 || multipleSelection.length < 1) {  // 判断当前已选数目
        _self.$message({
          message: '请至多选中一条记录',
          type: 'error'
        })
        return
      }
      var row = multipleSelection[0]
      _self.id = row.userId
      _self.orgId = row.orgId
      _self.dialogFormVisible = true
      _self.editForm = Object.assign({}, row)  // 把选中用户信息显示到模态框
      _fetch({  // 获取当前用户所在的机构信息
        url: 'http://10.21.23.126:8080/user/getUserEditInfo',
        type: 'post',
        data: {
          userId: _self.id
        }
      })
      .then((res) => {
        _self.provinceId = res.data.object.orgProvinceId
        _self.editForm.province = res.data.object.orgProvince
        _self.cityId = res.data.object.orgCityId
        _self.editForm.city = res.data.object.orgCity
        _self.areaId = res.data.object.orgAreaId
        _self.editForm.area = res.data.object.orgArea
        _fetch({        // 获取用户机构所在省下的市
          url: 'http://10.21.23.126:8080/provinceCityArea//getCitiesByProvince',
          type: 'post',
          data: {
            provinceId: _self.provinceId
          }
        })
        .then((res) => {
          _self.cities = res.data.object
        })
        .catch((err) => {
          console.log(err)
        })
        _fetch({    // 获取用户机构所在市下的区
          url: 'http://10.21.23.126:8080/provinceCityArea/getAreasByCity',
          type: 'post',
          data: {
            cityId: _self.cityId
          }
        })
        .then((res) => {
          _self.areas = res.data.object
        })
        .catch((err) => {
          console.log(err)
        })
        _fetch({   // 获取用户机构所在区下的机构
          url: 'http://10.21.23.126:8080/org/listByAreaId',
          type: 'post',
          data: {
            areaId: _self.areaId
          }
        })
        .then((res) => {
          _self.orgs = res.data.object
        })
        .catch((err) => {
          console.log(err)
        })
      })
      .catch((err) => {
        console.log(err)
      })
    },
    removeSelection: function (table) {
      var _self = this
      var multipleSelection = this.multipleSelection
      if (multipleSelection.length < 1) {    // 判断已选数目
        _self.$message({
          message: '请至少选中一条记录',
          type: 'error'
        })
        return
      }
      var ids = ''
      var id = []
      for (var i = 0; i < multipleSelection.length; i++) {
        var row = multipleSelection[i]
        ids += ' ' + row.username + ' '    // 保存已选用户的用户名
        id[i] = row.userId  // 保存已选用户的userId
      }
      console.log(id)
      this.$confirm('确认删除' + ids + '吗?', '提示', {
        type: 'warning'
      }).then(function () {
        _fetch({
          url: 'http://10.21.23.126:8080/user/delete',
          type: 'post',
          data: {
            ids: id
          }
        })
        .then((res) => {
          _self.$message({
            message: ids + '删除成功',
            type: 'success'
          })
          _self.loadingData()
        })
        .catch((err) => {
          _self.$message({
            message: ids + '删除失败',
            type: 'error'
          })
          console.log(err)
        })
      }).catch(function (e) {
        if (e !== 'cancel') {
          console.log('出现错误：' + e)
        }
      })
    },
    pageSizeChange: function (val) {    // 更换每页显示条数
      var _self = this
      _self.pageSize = val
      _self.loadingData()// 重新加载数据
    },
    currentPageChange: function (val) {   // 更换页码
      var _self = this
      _self.currentPage = val
      _self.loadingData()// 重新加载数据
    },
    cancelSubmit: function () {
      var _self = this
      _self.$refs['editForm'].resetFields() // 清空表单
      _self.editForm = Object.assign({}, null)
      _self.cities = []  // 清空相应下拉框
      _self.areas = []
      _self.orgs = []
      _self.dialogFormVisible = false
    },
    getProvince: function () {    // 获取所有省
      var _self = this
      _self.cities = []
      _self.areas = []
      _self.orgs = []
      _fetch({
        url: 'http://10.21.23.126:8080/provinceCityArea/getAllProvinces',
        type: 'post'
      })
      .then((res) => {
        _self.provinces = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getCity: function (val) {  // 获取相应省下的市
      var _self = this
      _self.editForm.city = ''    // 清空相应下拉框的已选中值
      _self.editForm.area = ''
      _self.editForm.orgName = ''
      _fetch({
        url: 'http://10.21.23.126:8080/provinceCityArea/getCitiesByProvince',
        type: 'post',
        data: {
          provinceId: val
        }
      })
      .then((res) => {
        _self.cities = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getArea: function (val) {    // 获取相应市下的区
      var _self = this
      _self.editForm.area = ''
      _self.editForm.orgName = ''
      _fetch({
        url: 'http://10.21.23.126:8080/provinceCityArea/getAreasByCity',
        type: 'post',
        data: {
          cityId: val
        }
      })
      .then((res) => {
        _self.areas = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getOrg: function (val) {   // 获取相应区下的机构
      var _self = this
      _self.editForm.orgName = ''
      _fetch({
        url: 'http://10.21.23.126:8080/org/listByAreaId',
        type: 'post',
        data: {
          areaId: val
        }
      })
      .then((res) => {
        _self.orgs = res.data.object
      })
      .catch((err) => {
        console.log(err)
      })
    },
    getOrgId: function (val) {    // 获取已选机构的机构id
      var _self = this
      _self.orgId = val
    }
  }
}
</script>

<style scoped>
.user {
  margin: 20px;
}
</style>



