<template>
  <div>
    <div style="display:flex">
      <el-input placeholder="输入关键字进行收索" suffix-icon="el-icon-search" v-model="filterText"></el-input>
    </div>
    <el-tree :data="treeData" :props="defaultProps" @node-click="handleNodeClick"
    node-key="id" :render-content="renderContent" :expand-on-click-node="false" ref="maintree" :filter-node-method="filterNode"></el-tree>
    <!-- 增加配电箱页面 -->
    <el-dialog :title="boxTitle" :visible.sync="addManagerVisible">
      <span class="Boxtitle">所属区：</span><span class="BoxMessage">{{boxArea}}</span>
      <span class="Boxtitle">所属公司：</span><span>{{boxCompany}}</span>
      <el-form :model="boxForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="电箱名称" prop="name">
          <el-input v-model="boxForm.name"></el-input>
        </el-form-item>
        <div style="display:flex;">
          <el-form-item label="电箱经纬度" prop="longitude">
            <el-input v-model="boxForm.longitude"></el-input>
          </el-form-item>
          <el-form-item label="" prop="latitude">
            <el-input v-model="boxForm.latitude" ></el-input>
          </el-form-item>
        </div>
        <el-form-item label="详细地址" prop="address">
          <el-input v-model="boxForm.address"></el-input>
        </el-form-item>
      </el-form>
      <Boxmap v-on:getLnglat='showLnglat' :boxData="mapData"></Boxmap>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addManagerVisible = false">取 消</el-button>
        <el-button type="primary" @click="addorUpdataBox('ruleForm')">{{boxButton}}</el-button>
      </div>
    </el-dialog>

    <!-- 增加或修改分组弹窗 -->
    <el-dialog :title="groupTitle" :visible.sync="gropwindowVisible" width="400px">
      <el-form :model="groupForm" :rules="groupRlues" ref="groupForm">
        <el-form-item label="分组名称" prop="name">
          <el-input v-model="groupForm.name" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="gropwindowVisible = false">取 消</el-button>
        <el-button type="primary" @click="sueraddGroup('groupForm')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 移动分组弹窗 -->

    <el-dialog :title="removeGroupTitle" :visible.sync="removeGroupVisible" width="400px">
      <el-tree :data="treeData" :props="defaultProps" @node-click="groupChoose" highlight-current
        node-key="id" :expand-on-click-node="false" :filter-node-method="groupFilterNode" ref="groupTree"></el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="removeGroupVisible = false">取 消</el-button>
        <el-button type="primary" @click="suerRemoveGroup">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Boxmap from '../map/map.vue'
import _fetch from '../../../../util/fetch'
export default {

  data () {
    return {
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      addManagerVisible: false,
      boxArea: '',
      boxCompany: '',
      boxForm: {
        name: '',
        longitude: '',
        latitude: '',
        address: ''
      },
      rules: {
        name: [
           {required: true, message: '请输入电箱名称', trigger: 'blur'}
        ],
        longitude: [
          {required: true, message: '请在地图上选择经度', trigger: 'blur'}
        ],
        latitude: [
          {required: true, message: '请在地图上选择纬度', trigger: 'blur'}
        ],
        address: [
          {required: true, message: '详细地址不能为空', trigger: 'blur'}
        ]
      },
      orgId: '',
      electricBoxId: '',
      boxTitle: '',
      boxButton: '',
      addorUpdataBoxNum: '',
      electricBoxdata: {},
      treeNodeParent: '',
      activeUpdata: '',
      mapData: '',
      parentId: '',
      treeData: [],
      gropwindowVisible: false,
      groupForm: {},
      activeToGroup: {},
      groupTitle: '',
      groupActiveType: '',
      activeMoveData: {},
      removeGroupVisible: false,
      removeGroupTitle: '',
      theRemoveGroupData: '',
      endRemoveGroupData: {},
      filterText: '',
      groupRlues: {
        name: [
          {required: true, message: '请输入活动名称', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    handleNodeClick (data, node) {
      this.treeNodeParent = node.parent.label
      let self = this
      this.activeMoveData = data
      console.log(data)
      console.log(data)
      console.log(data)
      console.log(data)
      console.log(data)
      // 判断用户点击的树节点类型，3是电箱，2是设备，1是分组
      if (data.typeId === 3) {
        this.electricBoxId = data.id
        this.orgId = data.orgId
        // 查询并保存电箱的数据
        _fetch({
          url: '/electricBox/updateSelect',
          type: 'post',
          data: {
            id: this.electricBoxId
          }
        })
        .then((res) => {
          this.electricBoxdata = res.data.object
          self.$emit('getElectricBoxdata', this.electricBoxdata, this.treeNodeParent)
        })
        // 请求电箱数据
        _fetch({
          url: '/device/list',
          type: 'post',
          data: {
            electricBoxId: this.electricBoxId,
            pageNo: 1,
            pageSize: 4
          }
        })
        .then((res) => {
          self.$emit('getElectricBox', res.data.object, this.electricBoxId, res.data.totalData)
        })
      } else if (data.typeId === 2 || data.typeId === 1) {
        this.orgId = data.orgId
        this.parentId = data.id
      }
    },
    // 显示操作反馈信息
    feedbackMessage (message, type) {
      this.$message({
        showClose: true,
        message: message,
        type: type
      })
    },
    // 根据不同节点类型渲染不同节点
    renderContent (h, { node, data, store }) {
      if (data.typeId === 3) {
        return (
          <span id = "treeNote" style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;" on-mouseover={(event) => this.mouseEnter(event)} on-mouseleave={(event) => this.mouseOut(event)}>
            <span>
              <span>{data.label}</span>
            </span>
            <span style="display:none">
              <el-tooltip class="item" effect="dark" content="编辑" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.updataBox(node, data) }><i class="el-icon-edit"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.isRemoveBox(node, data) }><i class="el-icon-delete"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="移动分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.isRemoveGroup(node, data) }><i class="el-icon-refresh"></i></el-button>
              </el-tooltip>
            </span>
          </span>)
      } else if (data.typeId === 2) {
        return (
          <span id = "treeNote" style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;" on-mouseover={(event) => this.mouseEnter(event)} on-mouseleave={(event) => this.mouseOut(event)}>
            <span>
              <span>{data.label}</span>
            </span>
            <span style="display:none">
              <el-tooltip class="item" effect="dark" content="增加分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.addGroup(node, data) }><i class="el-icon-document"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="移动分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.isRemoveGroup(node, data) }><i class="el-icon-refresh"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="添加配电箱" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.appendBox(node, data) }><i class="el-icon-circle-plus-outline"></i></el-button>
              </el-tooltip>
            </span>
          </span>)
      } else if (data.typeId === 1 && data.orgId !== null) {
        return (
          <span id = "treeNote" style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;" on-mouseover={(event) => this.mouseEnter(event)} on-mouseleave={(event) => this.mouseOut(event)}>
            <span>
              <i class="el-icon-menu"></i>
              <span>{data.label}</span>
            </span>
            <span style="display:none">
              <el-tooltip class="item" effect="dark" content="增加分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.addGroup(node, data) }><i class="el-icon-document"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="删除分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.deleteGroup(node, data) }><i class="el-icon-delete"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="修改分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.updataGroup(node, data) }><i class="el-icon-edit"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="添加配电箱" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.appendBox(node, data) }><i class="el-icon-circle-plus-outline"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="移动分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.isRemoveGroup(node, data) }><i class="el-icon-refresh"></i></el-button>
              </el-tooltip>
            </span>
          </span>)
      } else {
        return (
           <span id = "treeNote" style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;" on-mouseover={(event) => this.mouseEnter(event)} on-mouseleave={(event) => this.mouseOut(event)}>
            <span>
              <span>{node.label}</span>
            </span>
            <span style="display:none">
              <el-tooltip class="item" effect="dark" content="增加分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.addGroup(node, data) }><i class="el-icon-document"></i></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="移动分组" placement="top-start">
                <el-button style="font-size: 12px;" type="text" on-click={ () => this.isRemoveGroup(node, data) }><i class="el-icon-refresh"></i></el-button>
              </el-tooltip>
            </span>
          </span>)
      }
    },
    // 鼠标进入显示操作按钮
    mouseEnter (event) {
      if (event.target.id === 'treeNote') {
        event.target.childNodes[1].style.display = ''
      }
    },
    // 鼠标出来隐藏操作按钮
    mouseOut (event) {
      if (event.target.id === 'treeNote') {
        event.target.childNodes[1].style.display = 'none'
      }
    },
    // 增加配电箱
    appendBox (node, data) {
      this.boxArea = node.parent.label
      this.boxCompany = data.label
      this.boxTitle = '增加配电箱'
      this.boxButton = '确 定'
      this.addManagerVisible = true
      this.mapData = {
        longitude: 113.39888,
        latitude: 23.03747
      }
      this.addorUpdataBoxNum = 1
      this.boxForm = {name: '', longitude: '', latitude: '', address: ''}
    },
    // 得到子地图组件传来的数据
    showLnglat (lng, lat, address) {
      this.boxForm.longitude = lng
      this.boxForm.latitude = lat
      this.boxForm.address = address
      this.$nextTick()
    },
    // 确定添加或修改配电箱
    addorUpdataBox (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 向服务器发送新增电箱信息
          if (this.addorUpdataBoxNum === 1) {
            _fetch({
              url: '/electricBox/add',
              type: 'post',
              data: {
                name: this.boxForm.name,
                address: this.boxForm.address,
                orgId: this.orgId,
                parentId: this.parentId,
                longitude: this.boxForm.longitude,
                latitude: this.boxForm.latitude
              }
            })
            .then((res) => {
              // 重新刷新树
              _fetch({
                url: '/dataTree/list?treeType=2',
                type: 'post',
                data: {
                }
              })
              .then((res) => {
                this.treeData = res.data.object
                this.addManagerVisible = false
                this.boxForm = {name: '', longitude: '', latitude: '', address: ''}
              })
            })
          } else {
            // 返回修改后的电箱信息
            _fetch({
              url: '/electricBox/update',
              type: 'post',
              data: {
                name: this.boxForm.name,
                address: this.boxForm.address,
                orgId: this.boxForm.orgId,
                longitude: this.boxForm.longitude,
                latitude: this.boxForm.latitude,
                id: this.electricBoxId
              }
            })
            .then((res) => {
              this.addManagerVisible = false
              this.boxForm = {name: '', longitude: '', latitude: '', address: ''}
              // 重新刷新树
              _fetch({
                url: '/dataTree/list?treeType=2',
                type: 'post',
                data: {
                }
              })
              .then((res) => {
                this.treeData = res.data.object
              })
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 删除电箱
    isRemoveBox () {
      this.$confirm('此操作将永久删除该电箱, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
      // 向服务器发送删除电箱信息
        _fetch({
          url: '/electricBox/delete',
          type: 'post',
          data: {
            id: this.electricBoxId
          }
        })
        .then((res) => {
          // 重新刷新树
          _fetch({
            url: '/dataTree/list?treeType=2',
            type: 'post',
            data: {
            }
          })
          .then((res) => {
            this.treeData = res.data.object
          })
        })
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 编辑电箱
    updataBox (node, data) {
      this.boxArea = node.parent.label
      this.boxCompany = data.label
      this.boxTitle = '修改配电箱'
      this.boxButton = '修 改'
      this.addorUpdataBoxNum = 2
      var boxId = data.id
      this.activeUpdata = 1
      // 查询电箱的数据
      _fetch({
        url: '/electricBox/updateSelect',
        type: 'post',
        data: {
          id: boxId
        }
      })
      .then((res) => {
        this.mapData = {
          latitude: res.data.object.latitude,
          longitude: res.data.object.longitude
        }
        this.boxForm = res.data.object
        this.addManagerVisible = true
      })
    },

    // 增加分组
    addGroup (node, data) {
      this.groupForm.name = ''
      this.groupTitle = '增加分组'
      this.gropwindowVisible = true
      this.activeToGroup = data
      this.groupActiveType = 1
    },

    // 确定增加或修改分组
    sueraddGroup (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.groupActiveType === 1) {
            // 发送增加分组数据
            _fetch({
              url: '/dataTree/addGroup',
              type: 'post',
              data: {
                name: this.groupForm.name,
                parentId: this.activeToGroup.id,
                orgId: this.activeToGroup.orgId
              }
            })
            .then((res) => {
              // 重新刷新树
              _fetch({
                url: '/dataTree/list?treeType=2',
                type: 'post',
                data: {
                }
              })
              .then((res) => {
                this.treeData = res.data.object
                this.gropwindowVisible = false
              })
            })
          } else if (this.groupActiveType === 2) {
            // 发送修改分组数据
            _fetch({
              url: '/dataTree/update',
              type: 'post',
              data: {
                name: this.groupForm.name,
                parentId: this.activeToGroup.parentId,
                orgId: this.activeToGroup.orgId,
                id: this.activeToGroup.id,
                typeId: this.activeToGroup.typeId
              }
            })
            .then((res) => {
              // 重新刷新树
              _fetch({
                url: '/dataTree/list?treeType=2',
                type: 'post',
                data: {
                }
              })
              .then((res) => {
                this.treeData = res.data.object
                this.gropwindowVisible = false
              })
            })
          }
        } else {
          this.$message.error('请填写完整数据')
          return false
        }
      })
    },

    // 删除分组
    deleteGroup (node, data) {
      this.$confirm('此操作将永久删除该分组, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
      // 向服务器发送删除分组信息
        _fetch({
          url: '/dataTree/deleteGroup',
          type: 'post',
          data: {
            groupId: data.id
          }
        })
        .then((res) => {
          if (res.data.object === -1) {
            this.$message.error('删除失败，请先删除其子节点')
          } else if (res.data.object) {
            // 重新刷新树
            _fetch({
              url: '/dataTree/list?treeType=2',
              type: 'post',
              data: {
              }
            })
            .then((res) => {
              this.treeData = res.data.object
              this.$message.success('删除成功')
            })
          }
        })
      })
    },
    // 修改分组
    updataGroup (node, data) {
      this.groupTitle = '修改分组'
      this.gropwindowVisible = true
      this.groupForm.name = data.label
      this.groupActiveType = 2
      this.activeToGroup = data
    },
    // 是否移动分组
    isRemoveGroup (node, data) {
      this.removeGroupVisible = true
      this.removeGroupTitle = '移动 ' + data.label + ' 至分组'
      this.theRemoveGroupData = data
      // this.$refs.groupTree.filter()
    },
    // 选择要移动到的分组
    groupChoose (data) {
      this.endRemoveGroupData = data
    },
    // 确定移动分组
    suerRemoveGroup () {
      let parentOrgId = (this.endRemoveGroupData.orgId === null) ? '' : this.endRemoveGroupData.orgId
      let orgId = (this.theRemoveGroupData.orgId === null) ? '' : this.endRemoveGroupData.orgId
      // 发送数据给后台
      _fetch({
        url: '/dataTree/update',
        type: 'post',
        data: {
          name: this.theRemoveGroupData.label,
          id: this.theRemoveGroupData.id,
          parentId: this.endRemoveGroupData.id,
          parentOrgId: parentOrgId,
          typeId: this.theRemoveGroupData.typeId,
          orgId: orgId
        }
      })
      .then((res) => {
        if (res.data.object === -2) {
          this.$message.error('错了哦，不可以把机构内的数据移出机构外，请重新选择')
        } else if (res.data.object === -3) {
          this.$message.error('错了哦，请勿将结点移动到其父结点下,请重新选择')
        } else if (res.data.object !== 0 || res.data.object !== -1 || res.data.object !== -2 || res.data.object !== -3) {
          // 重新刷新树
          _fetch({
            url: '/dataTree/list?treeType=2',
            type: 'post',
            data: {
            }
          })
          .then((res) => {
            this.treeData = res.data.object
            this.removeGroupVisible = false
          })
        }
      })
    },
    // 筛选树节点信息
    filterNode (value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    // 移动树分组中已经选择的分组
    groupFilterNode (val, data) {
      if (data.id === this.theRemoveGroupData.id) {
        return true
      }
    }
  },
  components: {
    Boxmap
  },
  props: ['data'],
  watch: {
    'data': function (data) {
      this.treeData = data
    },
    filterText (val) {
      this.$refs.maintree.filter(val)
    }
  }
}
</script>

<style>
.Boxtitle {
  font-size:17px;
}
.BoxMessage {
  margin-right:30px;
}
</style>


