<template>
  <div id="app">
    <!-- 后台管理界面 -->
    <div v-if="isLogin">
      <audio ref="audio" @ended="closeNotification" :src="'data:audio/mpeg;base64,' + voiceBase64String"></audio>
      <div class="header">
        <div class="logo"><img src="/static/img/logo.png" /></div>
        <navigation></navigation>
        <div class="loginInfo">
          <span>{{ userObj.username }}</span>
          <a @click="logout" href="javascript:(0);">退出</a>
        </div>
      </div>
      <div class="container">
        <router-view></router-view>
      </div> 
    </div>
    <!-- 登录页面 -->
    <login v-else></login>
  </div>
</template>

<script>
import navigation from './components/common/navigation'
import login from './components/login/index'
import { mapState, mapMutations } from 'vuex'
import config from './config'

export default {
  name: 'app',
  components: { navigation, login },
  data () {
    return {
      voiceBase64String: undefined, // 声音的64base编码
      voiceBase64StringArr: [], // 暂存未播放的语音
      notification: '' // 储存报警的实例
    }
  },
  watch: {
    isLogin: function () {
      this.createVoiceSocket()
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    open () {
      this.isCollapse = !this.isCollapse
    },
    // 退出登录
    logout () {
      // 清除store中的登录信息，isLogin置为false，跳转路由到登录页面
      this.setLoginState(false)
      this.$router.push('/login')
      console.log('退出登录')
    },
    ...mapMutations(['setLoginState']),
    // 建立socket
    createVoiceSocket () {
      let _this = this
      console.log('开始连接socket')
      // console.log('url', config.baseURL + config.socketURL)
      var socket = new window.SockJS(config.baseURL + config.socketURL) // portfolio为后台接口，问清楚他们
      var stompClient = window.Stomp.over(socket)
      stompClient.connect({}, function () {
        stompClient.subscribe(config.socketVoiceURL, function (message) {
          // 弹框同时 响音乐。但是问题是，怎么同步??? 或者说。我那个框里面放html
          // 语音播放的时候，要弹框就好了
          let voiceData = message.body
          let audioElem = _this.$refs['audio']
          if (_this.voiceBase64String === undefined) {
            _this.voiceBase64String = voiceData
            _this.notify()
            // _this.$refs.audio.play()  判断资源加载完后 播放
            // 重新加载
            audioElem.load()
            audioElem.oncanplaythrough = function () {
              audioElem.play()
            }
          } else {
            // 如果当前还有在播放的，就塞到待播放数组中
            _this.$message('push now')
            _this.voiceBase64StringArr.push(voiceData)
          }
        })
      })
    },
    // 当音频播放完的时候，关闭当前notification实例
    closeNotification () {
      this.notification.close()
    },
    // 消息通知弹框
    notify (message) {
      let _this = this
      // message改为后台传来的
      this.notification = this.$notify({
        title: '报警',
        dangerouslyUseHTMLString: true,
        message: `<span>您有新的报警信息</span><el-button class="el-button el-button--primary warningBtn el-button--text"">点击前往</el-button>`,
        position: 'bottom-right',
        type: 'warning',
        duration: 20000,
        onClose () {
          // 播放下一条语音
          // console.log('点击关闭，播放下一条语音')
          _this.nextVoice()
        },
        onClick () {
          // 点击后前往monitor页面，关闭通知栏
          _this.turnToMonitor()
          this.close()
        }
      })
    },
    // 播放下一个语音
    nextVoice () {
      let audioElem = this.$refs['audio']
      // 如果数组中有东东，就拿出来 播放
      if (this.voiceBase64StringArr.length > 0) {
        this.voiceBase64String = this.voiceBase64StringArr.shift()
        this.notify()
        audioElem.load()
        audioElem.oncanplaythrough = function () {
          audioElem.play()
        }
      } else {
        this.voiceBase64String = undefined
      }
    },
    // 跳转到报警的路由(monitor)
    turnToMonitor () {
      this.$router.push('/monitor')
    }
  },
  computed: {
    ...mapState(['userObj', 'isLogin'])
  }
}
</script>

<style>
@import "../static/font/font.css";
html, body {
  margin: 0 ;
  padding: 0;
  font-family: 'PingFang-SC-Regular';
  height: 100%;
}
#app {
  height: 100%;
}
a {
  text-decoration: none;
}
/* 顶部 */
.header {
  display: flex;
  justify-content: space-between;
  padding: 10px 57px 10px 40px;
  align-items: center;
  box-shadow: 0px 1px 10px #eeebeb;
  height: 80px;
}
.logo {
  height: 50px;
}
.logo > img {
  width: 100%;
  height: 100%;
}
.loginInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.loginInfo > span {
  margin-right: 45px;
  font-size: 14px;
}
.loginInfo > a{
  font-size: 14px;
  color: #f56c6c;
}

/* 实时监控 树形图的样式 */
.box .middleLeft .el-tree-node:focus>.el-tree-node__content, .el-tree-node__content:hover {
  background-color: #cfe7ff;
}
.el-tree-node__content {
  height: 29px;
}
.warningBtn {
  margin-left: 40px;
}
</style>



