<template>
  <div class="amap-page-container">
    <el-amap
      vid="amapDemo"  
      :center="[boxData.longitude, boxData.latitude]"
      :zoom="zoom"  
      class="amap-demo"
      :events="events">
      <el-amap-marker :position="[boxData.longitude, boxData.latitude]" :visible="oldMarker"></el-amap-marker>
      <el-amap-marker :position="marker.position" :visible="marker.visible"></el-amap-marker>
    </el-amap>
  </div>
</template>

<style>
  .amap-demo {
    height: 300px;
  }
</style>

<script>
export default {
  data () {
    let self = this
    return {
      zoom: 12,
      center: [113.39888, 23.03747],
      address: '',
      oldMarker: true,
      events: {
        click (e) {
          let { lng, lat } = e.lnglat
          self.lng = lng
          self.lat = lat
          self.marker.visible = true
          self.marker.position = [lng, lat]
          self.oldMarker = false
          // 这里通过高德 SDK 完成。
          var geocoder = new window.AMap.Geocoder({
            radius: 1000,
            extensions: 'all'
          })
          geocoder.getAddress([lng, lat], function (status, result) {
            if (status === 'complete' && result.info === 'OK') {
              if (result && result.regeocode) {
                self.address = result.regeocode.formattedAddress
                self.$nextTick()
                self.$emit('getLnglat', self.lng, self.lat, self.address)
              }
            }
          })
        }
      },
      lng: 0,
      lat: 0,
      marker: {
        position: [113.39888, 23.03747],
        visible: false
      }
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
  },
  props: ['boxData']
}
</script>


