<template>
  <div class="navigation">
    <el-menu :router="true" active-text-color="#409eff" text-color="#000000" :default-active="$route.path" class="el-menu-demo" mode="horizontal">
      <el-menu-item index="/monitor">实时监控</el-menu-item>
      <el-menu-item index="/equipment">设备管理</el-menu-item>
      <el-menu-item index="/warning">预警管理</el-menu-item>
      <el-submenu index="/company" >
        <template slot="title" style="border-bottom-color: rgb(64, 158, 255); color: rgb(64, 158, 255);">系统管理</template>
        <el-menu-item index="/company">公司管理</el-menu-item>
        <el-menu-item index="/user">用户管理</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'navigation',
  data () {
    return {
      activeIndex: '3'
    }
  }
}
</script>

<style scoped>
.navigation {
  margin-left: -40px;
}
.el-menu {
  display: flex;
  justify-content: center;
}
</style>
<style>
/* 改变导航栏组件原来的样式
  我也不知道为啥写在navigation组件无效 scoped的原因吗
  加个.header是为了不污染其他导航栏组件的样式 */
.header .el-menu-item, .header .el-submenu__title {
  transition: border-color .3s,background-color .3s,color .3s, text-shadow .3s;
}
/* 字体阴影 */
.header .el-menu-item.is-active, .header .el-submenu.is-active .el-submenu__title {
  text-shadow: 0px 0px 10px rgb(164, 204, 238);
}
.header .el-menu-item, .header .el-submenu__title {
  font-size: 20px;
}
.header .el-menu--horizontal {
  border: none;
}
.header .el-menu--horizontal>.el-menu-item.is-active, .header .el-menu--horizontal>.el-submenu.is-active .el-submenu__title {
  border-bottom: none;
}
.header .el-menu--horizontal .el-menu-item {
  border-bottom: none;
}
.header .el-menu--horizontal .el-submenu .el-submenu__title {
  border-bottom: none;
}
</style>



